import '@servicenow/now-date-range-picker';
import { createCustomElement } from '@servicenow/ui-core';

import { toMoment, getStartEnd } from './helpers';
import styles from './daterange-panel.scss';

export const PAR_FILTER_DATERANGE_PANEL__VALUE_SET = 'PAR_FILTER_DATERANGE_PANEL#VALUE_SET';

const today = new Date()
	.toISOString()
	.replace(/T.*$/, '');

const injectStyles = ({ elm }) => {
	const style = document.createElement('style');
	style.innerHTML = '.panel-container { border: 0 !important; box-shadow: none !important; }';
	elm.shadowRoot.appendChild(style);
};

const view = ({ properties, month }) => {
	const {
		start,
		end,
	} = properties;

	// DEF0174386: Safari doesn't render `sn-controls-standard-range` without having `now-date-range-picker` in the DOM.
	// Not sure why this happens (doesn't happen in Chome or Firefox), and sounds like a bug for the Seismic team (or NDS)
	// to solve. But we don't have time for that, so that's why there's a hidden <div> below.

	return (
		<div className="daterange-panel-container">
			<div style={{display:'none'}}><now-date-range-picker start="10-01-2005" end="11-03-2005" preset="custom" /></div>
			<sn-controls-standard-range
				hook-insert={injectStyles}
				start={toMoment(start)}
				end={toMoment(end)}
				visibleMonth={toMoment(month)}
				format="YYYY-MM-DD"
			/>
		</div>
	);
};

createCustomElement('par-filter-daterange-panel', {
	properties: {
		/**
		 * Start date of the range.
		 * @type {(string|undefined)}
		 */
		start: {},
		/**
		 * End date of the range.
		 * @type {(string|undefined)}
		 */
		end: {},

		/**
		 * OnSelected function.
		 *
		 * Gets called every time the start or end is updated. Receives
		 * two parameters: start, end
		 * @type {Function}
		 */
		onSelected: {},
	},
	view,
	styles,
	initialState: {
		month: today,
		highlight: today,
	},
	actionHandlers: {
		'DATE_RANGE_PICKER#SET_START_AND_END_STATE' ({ action, properties, dispatch }) {
			const { start, end } = properties;
			const { payload: { start: date } } = action;
			const [newStart, newEnd] = getStartEnd(start, end, date);

			dispatch(PAR_FILTER_DATERANGE_PANEL__VALUE_SET, { start: newStart, end: newEnd });
		},

		'DATE_RANGE_PICKER#SET_END_STATE' ({ action, properties, dispatch }) {
			const { start } = properties;
			const { payload: { newDate: end } } = action;

			dispatch(PAR_FILTER_DATERANGE_PANEL__VALUE_SET, { start, end });
		},

		'DATE_RANGE_PICKER#SET_VISIBLE_MONTH' ({ action, updateState }) {
			const { payload: { visibleMonth } } = action;
			updateState({
				month: toMoment(visibleMonth),
			});
		},
	},
});
