import { t } from 'sn-translate';
import { actionTypes } from '@servicenow/ui-core';

import styles from './advanced-options.scss';

const PAR_FILTER_ADVANCED_OPTIONS__CLOSE = 'PAR_FILTER_ADVANCED_OPTIONS#CLOSE';
const PAR_FILTER_ADVANCED_OPTIONS__SAVE = 'PAR_FILTER_ADVANCED_OPTIONS#SAVE';

export const advancedOptionsView = ({
	dirty, emptyOptionCurrent, encodedQueryCurrent, properties,
}) => {
	const {
		source: { table, label, type },
		encodedQuery,
		emptyOption,
	} = properties;
	const footerActions = [
		{
			label: t('Save'), variant: 'primary', id: 'save', disabled: !dirty,
		},
		{ label: t('Cancel'), variant: 'secondary', id: 'cancel' },
	];

	// I have to do it because seismic can't set initial state from properties, so I have to updateState myself in CONNECTED lifecycle.
	if (emptyOptionCurrent === null && encodedQueryCurrent === null) {
		return null;
	}

	return (
		<now-modal
			headerLabel={t('Configure values of {0} [{1}]', label, table)}
			footerActions={footerActions}
			size="lg"
			opened
			legacyMode
		>
			<p className="heading-secondary">
				{t('Let\'s define the values that appear in the filter.')}
			</p>
			<span className="text-hint">
				{t('You can use specific values or use dynamic conditions based on the user\'s profile. For example, Assigned to is (dynamic) me')}
			</span>
			<div className="condition-builder-container">
				<now-condition-builder
					tableName={table}
					tableLabel={label}
					encodedQuery={encodedQuery}
					hideRelatedListQueryConditions={table === 'sys_choice'}
				/>
			</div>

			{type === 'reference' ? (
				<div className="empty-option-container">
					<par-filter-config-empty-option emptyOption={emptyOption} />
				</div>
			) : null}
		</now-modal>
	);
};

export const advancedOptions = {
	initialState: {
		encodedQueryDirty: false,
		emptyOptionDirty: false,
		encodedQueryCurrent: null,
		emptyOptionCurrent: null,
		dirty: false,
	},
	properties: {
		encodedQuery: { default: '' },
		source: {},
		emptyOption: { default: '' },
	},
	view: advancedOptionsView,
	styles,
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED] ({ properties, updateState }) {
			updateState({
				encodedQueryCurrent: properties.encodedQuery,
				emptyOptionCurrent: properties.emptyOption,
			});
		},
		'NOW_MODAL#OPENED_SET' ({ dispatch }) {
			dispatch(PAR_FILTER_ADVANCED_OPTIONS__CLOSE);
		},
		'NOW_MODAL#FOOTER_ACTION_CLICKED' ({ state, action, dispatch }) {
			const { emptyOptionCurrent, encodedQueryCurrent } = state;
			const { payload } = action;

			if (payload.action.id === 'cancel') {
				dispatch(PAR_FILTER_ADVANCED_OPTIONS__CLOSE);
			} else {
				const payload = { encodedQuery: encodedQueryCurrent, emptyOption: emptyOptionCurrent };
				dispatch(PAR_FILTER_ADVANCED_OPTIONS__SAVE, payload);
			}
		},

		'NOW_CONDITION_BUILDER#ENCODED_QUERY_SET' ({
			action, state, properties, updateState,
		}) {
			const { payload: { value } } = action;
			const { emptyOptionDirty } = state;
			const { encodedQuery } = properties;
			const encodedQueryDirty = value !== encodedQuery;

			updateState({
				encodedQueryDirty,
				encodedQueryCurrent: value,
				dirty: encodedQueryDirty || emptyOptionDirty,
			});
		},

		'PAR_FILTER_EMPTY_OPTION#OPTION_SET' ({
			action, state, properties, updateState,
		}) {
			const { payload: { value } } = action;
			const { encodedQueryDirty } = state;
			const { emptyOption } = properties;
			const emptyOptionDirty = value !== emptyOption;

			updateState({
				emptyOptionDirty,
				emptyOptionCurrent: value,
				dirty: emptyOptionDirty || encodedQueryDirty,
			});
		},
	},
};
