/* eslint brace-style: 0 */
import { t } from 'sn-translate';
import orderBy from 'lodash/orderBy';

import { ListSearch } from '../list-search';
import { ListSort } from '../list-sort';
import { ListError } from '../list-error';

import styles from './list-single.scss';
import { NOW_FILTER__SET_DIRTY } from '../../constants';
import searchItems from '../../../helpers/searchItems';

const PAR_FILTER_LIST_SINGLE__SELECT_ITEM = 'PAR_FILTER_LIST_SINGLE#SELECT_ITEM';
const PAR_FILTER_LIST_SINGLE__CHECK_DIRTY = 'PAR_FILTER_LIST_SINGLE#CHECK_DIRTY';
const PAR_FILTER_LIST_SINGLE__PUSH_DIRTY = 'PAR_FILTER_LIST_SINGLE#PUSH_DIRTY';

const properties = {
	items: {},
	selectedIds: {
		default: [],
	},
	sort: {},
	search: {},
	loading: {},
	errorMessage: {},
	onQuery: {},
};

const initialState = {
	selectedItem: null,
	appliedItems: null,
};

const Loading = ({ state }) => (state ? (
	<div className="loading">
		<now-loader size="lg" />
	</div>
) : null);

const NoItems = ({ condition }) => (condition ? (
	<div className="no-content">{t('Nothing is available')}</div>
) : null);

function renderAppliedItems (appliedItems) {
	if (!appliedItems || appliedItems.length === 0) {
		return null;
	}

	// At this point only render first group label, and ignore all possible others
	const prefix = appliedItems.length && appliedItems[0].groupsLabels ? `${appliedItems[0].groupsLabels[0]}: ` : '';

	const appliedItemsBlock = appliedItems.length ? appliedItems.map(({ label }) => (
		<now-pill className="pill" label={`${prefix}${label}`} size="sm" />
	)) : null;

	return (
		<div key="appliedItems" className="applied-items">
			<h3 className="title">{t('Applied')}</h3>
			<div data-testAppliedItems>{appliedItemsBlock}</div>
		</div>
	);
}

export const listSingleView = ({ appliedItems, properties }, { dispatch }) => {
	const {
		items,
		loading,
		errorMessage,
		sort,
		search,
		onQuery,
	} = properties;

	const setSelectedItem = (data) => dispatch(PAR_FILTER_LIST_SINGLE__SELECT_ITEM, { data });
	let availableItems = items;
	let isGroupedData = false;

	if (availableItems) {
		availableItems = searchItems(availableItems, search);
		availableItems = orderBy(availableItems, ['label'], [sort.toLowerCase()]);
		isGroupedData = availableItems[0] && Array.isArray(availableItems[0].items);
	}

	const disabled = !availableItems || availableItems.length === 0;

	// If the data is grouped we need to find group name and use it in pill label
	if (isGroupedData && Array.isArray(appliedItems) && appliedItems.length) {
		const appliedId = appliedItems[0].id;
		const appliedGroups = items.reduce((prev, curr) => {
			const item = curr.items.some(({ id }) => id === appliedId);
			return item ? prev.concat(curr.label) : prev;
		}, []);

		appliedItems[0].groupsLabels = appliedGroups;
	}

	if (!loading && !items && !errorMessage) {
		return null;
	}

	if (errorMessage) {
		return (
			<ListError errorMessage={errorMessage} />
		);
	}

	return (
		<div className="list-single-container">
			{/* Applied item(s) pill */}
			{renderAppliedItems(appliedItems)}

			{/* Listbox with available items */}
			<div key="availableItems" className="available-items">
				<h3 className="title">{t('Available')}</h3>
				<div className="search-container">
					<ListSearch
						search={search}
						ariaLabel={t('Filter available items.')}
						onChange={(value) => onQuery({ search: value })}
					/>
					<ListSort
						testId="data-test-sort-button-available-items"
						sort={sort}
						onAction={onQuery}
						disabled={disabled}
					/>
				</div>
				<div className="items">
					<Loading state={loading} />
					<NoItems condition={availableItems && availableItems.length === 0 && !loading} />
					<par-filter-listbox
						multiple={false}
						items={availableItems}
						label={t('Available')}
						onChange={setSelectedItem}
					/>
				</div>
			</div>
		</div>
	);
};

const actionHandlers = {
	[PAR_FILTER_LIST_SINGLE__SELECT_ITEM] ({ action, updateState, dispatch }) {
		const { payload: { data } } = action;
		const selectedItem = data[0] || null;

		updateState({ selectedItem });
		dispatch(PAR_FILTER_LIST_SINGLE__CHECK_DIRTY);
	},

	[PAR_FILTER_LIST_SINGLE__CHECK_DIRTY] ({ state, properties, dispatch }) {
		const { selectedItem } = state;
		const { selectedIds } = properties;
		const dirty = selectedItem && selectedIds.length > 0 ? selectedItem.id !== selectedIds[0] : selectedItem !== null;

		dispatch(PAR_FILTER_LIST_SINGLE__PUSH_DIRTY, { dirty });
	},

	[PAR_FILTER_LIST_SINGLE__PUSH_DIRTY] ({
		action, state, dispatch,
	}) {
		const { selectedItem } = state;
		const { payload: { dirty } } = action;
		const targetItems = selectedItem ? [selectedItem] : [];

		dispatch(NOW_FILTER__SET_DIRTY, {
			dirty,
			dirtyBag: {
				selectedIds: targetItems.map((x) => x.id),
				selectedItems: targetItems,
			},
		});
	},
};

const transformState = (state) => {
	const { appliedItems, properties } = state;

	if (appliedItems !== null) {
		return state;
	}

	const { items, selectedIds } = properties;
	const isGroupedData = Array.isArray(items) && items.length && Array.isArray(items[0].items);
	let newAppliedItems = [];

	if (!isGroupedData) {
		newAppliedItems = items && Array.isArray(selectedIds) && items.filter((x) => selectedIds.includes(x.id));
	}
	else if (Array.isArray(selectedIds) && selectedIds.length > 0) {
		for (const group of items) {
			const items = group.items.filter((item) => selectedIds.includes(item.id));

			if (items.length) {
				newAppliedItems = [...newAppliedItems, ...items];
				break;
			}
		}
	}

	return {
		...state,
		appliedItems: (newAppliedItems && newAppliedItems.length) ? newAppliedItems : null,
	};
};

export const listSingle = {
	properties,
	initialState,
	transformState,
	actionHandlers,
	view: listSingleView,
	styles,
};
