import { t } from 'sn-translate';
import { Fragment } from '@seismic/snabbdom-renderer';
import { actionTypes } from '@servicenow/ui-core';

import styles from './default-values.scss';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const initialState = {
	search: '',
	dateranges: null,
};

const properties = {
	defaults: {},
	config: {},
	single: {},
};

const defaultValuesView = ({
	search,
	properties: {
		nowId, single, defaults, config,
	},
}) => {
	const {
		table,
		field,
		fieldType,
		encodedQuery,
		booleanLabels,
	} = config;
	const { start = '', end = '' } = defaults;

	if (fieldType.includes('date')) {
		return (
			<Fragment>
				<now-input
					type="text"
					value={start}
					label={t('Start')}
					pattern="(19|20)[0-9]{2}-[0-9]{2}-[0-9]{2}"
					placeholder="YYYY-MM-DD"
					append-to-payload={{
						type: 'start',
						end,
					}}
				/>
				<now-input
					type="text"
					value={end}
					label={t('End')}
					pattern="(19|20)[0-9]{2}-[0-9]{2}-[0-9]{2}"
					placeholder="YYYY-MM-DD"
					append-to-payload={{
						type: 'end',
						start,
					}}
				/>
			</Fragment>
		);
	}

	return (
		<Fragment>
			<par-filter-data-provider
				component-id={`${nowId}-config-data-provider`}
				table={table}
				field={field}
				fieldType={fieldType}
				search={search}
				sort="ASC"
				encodedQuery={encodedQuery}
				booleanLabels={booleanLabels}
				emptyOption={null}
			/>
			<now-typeahead-multi
				className="control"
				items={`@${nowId}-config-data-provider/resultData`}
				selectedItems={single ? defaults.slice(-1) : defaults}
				label={single ? t('Default value') : t('Default values')}
				helperContent={t('Define the values you want to be applied as default')}
				manageSelectedItems
			/>
		</Fragment>
	);
};

export const defaultValues = {
	initialState,
	properties,
	view: defaultValuesView,
	styles,
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED] ({ state, properties, updateState }) {
			const { dateranges } = state;
			const {
				defaults: {
					start = '',
					end = '',
				},
			} = properties;

			if (dateranges === null) {
				updateState({
					dateranges: {
						start,
						end,
					},
				});
			}
		},

		'NOW_TYPEAHEAD_MULTI#SELECTED_ITEMS_SET' ({ action, properties, dispatch }) {
			const { payload: { value } } = action;
			const { single } = properties;
			const payload = { value: single ? value.slice(-1) : value };

			action.stopPropagation();
			dispatch('PAR_FILTER_CONFIG_DEFAULT_VALUES#ITEMS_SET', payload);
		},

		'NOW_TYPEAHEAD#SELECTED_ITEM_SET' ({ action, dispatch }) {
			const { payload } = action;
			const value = [payload.item];

			action.stopPropagation();
			dispatch('PAR_FILTER_CONFIG_DEFAULT_VALUES#ITEMS_SET', { value });
		},

		'NOW_INPUT#VALUE_SET' ({ state, action, dispatch }) {
			const { dateranges } = state;
			const {
				payload: {
					value,
					type,
				},
			} = action;

			dateranges[type] = value;
			action.stopPropagation();

			if (
				(dateranges.start && dateranges.end) ||
				(!dateranges.start && !dateranges.end)
			) {
				dispatch('PAR_FILTER_CONFIG_DEFAULT_VALUES#ITEMS_SET', { value: dateranges });
			}
		},
	},
};
