import { actionTypes } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

import {
	NOW_FILTER__TOGGLED,
	NOW_FILTER__QUERIED,
	NOW_FILTER__SORTED,
	NOW_FILTER__APPLIED,
} from '../filter';

const { COMPONENT_ERROR_THROWN } = actionTypes;

const NOW_FILTER_CONNECTED__APPLIED = 'NOW_FILTER_CONNECTED#APPLIED';

const initialState = {
	error: null,
	search: '',
	shouldDataLoad: false,
};

export const filterConnected = {
	initialState,
	properties: {
		/**
		 * Name of the filter displayed as a label.
		 */
		label: {
			default: 'Filter',
		},

		table: {},
		field: {},
		fieldType: {},

		/**
		 * Single or multiple selection type of filter. Supported values are multi and single.
		 */
		listMode: {
			default: 'multi',
			schema: {
				type: 'string',
				enum: [
					'multi',
					'single',
				],
			},
		},

		/**
		 * Default sorting appeared when user expands dropdown
		 */
		sort: {
			default: 'ASC',
			schema: {
				type: 'string',
				enum: [
					'ASC',
					'DESC',
				],
			},
		},

		encodedQuery: {},
		emptyOption: {},
		booleanLabels: {},
		selectedIds: {},
		start: {},
		end: {},
	},
	actionHandlers: {
		[COMPONENT_ERROR_THROWN] ({ action: { payload }, updateState }) {
			updateState({
				error: payload,
			});
		},

		[NOW_FILTER__QUERIED] ({ action: { payload }, updateState }) {
			updateState({
				search:	payload.search,
			});
		},

		[NOW_FILTER__SORTED] ({ action: { payload }, updateProperties }) {
			updateProperties({
				sort:	payload.sort,
			});
		},

		[NOW_FILTER__TOGGLED] ({
			action, state, updateState,
		}) {
			const opened = action.payload.value;
			const { shouldDataLoad } = state;

			if (opened && !shouldDataLoad) {
				// This is the first toggle
				updateState({
					shouldDataLoad: true,
				});
			}
			if (!opened) {
				// This is when dropdown is closed we need to reset search query
				updateState({
					search: '',
				});
			}
		},
		[NOW_FILTER__APPLIED] ({
			action, dispatch, properties,
		}) {
			dispatch(NOW_FILTER_CONNECTED__APPLIED, {
				...action.payload,
				field: properties.field,
				table: properties.table,
			});
		},
	},
	view (state) {
		const {
			properties, error, search, shouldDataLoad,
		} = state;
		const {
			nowId,
			label,
			table,
			field,
			fieldType,
			listMode,
			encodedQuery,
			emptyOption,
			booleanLabels,
			sort,
			selectedIds,
			start,
			end,
		} = properties;

		const isDateFilter = /date/.test(fieldType);
		const selectType = fieldType && (isDateFilter ? 'daterange' : fieldType === 'boolean' ? 'boolean' : 'choice');
		const selectedIdValues = fieldType === 'boolean'
			? selectedIds.filter((id) => id === 'true' || id === 'false') // should leave only single value or nothing
			: selectedIds;

		if (error) {
			console.error('=== sn-component-filter::COMPONENT_ERROR_THROWN', error);
			return (
				<div>Error</div>
			);
		}

		return (
			<Fragment>
				{shouldDataLoad && !isDateFilter ? (
					<par-filter-data-provider
						component-id={`${nowId}-filter-data-provider`}
						table={table}
						field={field}
						fieldType={fieldType}
						search={search}
						sort={sort}
						encodedQuery={encodedQuery}
						emptyOption={emptyOption}
						booleanLabels={booleanLabels}
					/>
				) : null}
				<now-filter
					label={label}
					type={selectType}
					selectedIds={selectedIdValues}
					listMode={listMode}
					sort={sort}
					search={search}
					items={`@${nowId}-filter-data-provider/resultData`}
					loading={`@${nowId}-filter-data-provider/loading`}
					errorMessage={`@${nowId}-filter-data-provider/errorMessage`}
					start={start}
					end={end}
				/>
			</Fragment>
		);
	},
};
