import { t } from 'sn-translate';
import { debounce } from '../../../helpers/mockable-debounce';

export const handleInput = (value, onChange) => {
	const trimmed = value.trim();
	if (trimmed.length > 2) {
		onChange(trimmed);
	}
	if (trimmed === '') {
		onChange(trimmed);
	}
};

/**
 * This variable is lazy-initialized because we dont want to debounce be called on init of this module.
 * Because in unit test this debounce is mocked only after module is already loaded.
 */
let debouncedHandleInput;

export function ListSearch ({ search, onChange, ariaLabel }) {
	if (!debouncedHandleInput) {
		debouncedHandleInput = debounce(handleInput, 500);
	}

	return (
		<div className="list-search">
			<now-input
				data-testSearchInput
				className="search-input"
				configAria={{
					'aria-label': ariaLabel,
				}}
				placeholder={t('Search')}
				value={search}
				{...{
					'on-NOW_INPUT#INPUT': ({ detail }) => {
						debouncedHandleInput(detail.payload.fieldValue, onChange);
					},
				}}
			>
				<now-icon slot="start" className="search-icon" icon="magnifying-glass-fill" size="md" />
			</now-input>
		</div>
	);
}
