import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import styles from './boolean-labels.scss';

const properties = {
	labels: {
		default: {
			trueLabel: '',
			falseLabel: '',
		},
	},
};

const booleanValuesView = ({ properties }) => {
	const { labels: { trueLabel = '', falseLabel = '' } } = properties;

	return (
		<Fragment>
			<div className="boolean-option">
				<now-input
					className="control"
					label={t('Enter values for the filter element "true"')}
					helperContent={t('Viewer sees this value instead of the table value "true"')}
					value={trueLabel}
					appendToPayload={{ type: 'trueLabel' }}
				/>
			</div>

			<div className="boolean-option">
				<now-input
					className="control"
					label={t('Enter values for the filter element "false"')}
					helperContent={t('Viewer sees this value instead of the table value "false"')}
					value={falseLabel}
					appendToPayload={{ type: 'falseLabel' }}
				/>
			</div>
		</Fragment>
	);
};

export const booleanLabels = {
	properties,
	view: booleanValuesView,
	styles,
	actionHandlers: {
		'NOW_INPUT#VALUE_SET' ({ action, dispatch }) {
			const { payload: { value, type } } = action;
			const label = value.trim();

			action.stopPropagation();
			dispatch('PAR_FILTER_CONFIG_BOOLEAN_LABELS#LABELS_SET', { label, type });
		},
	},
};
