export const GET_CHOICES = {
	query: `
		query getChoices ($table: String!, $field: String!) {
			AppUnifilter_Query {
				getChoices (
					table: $table,
					field: $field,
				) {
					label
					id: value
		    }
			}
		}
	`,
	getData (data) {
		if (data && data.AppUnifilter_Query && data.AppUnifilter_Query.getChoices) {
			return data.AppUnifilter_Query.getChoices;
		}

		return null;
	},
};

export const GET_VALUES = {
	query: `
		query getFieldValues (
			$table: String!,
			$field: String!,
			$sort: String,
			$search: String,
			$encodedQuery: String,
			$maxNbRecords: Int = 100,
			$nextRecordIdx: Int = -1,
		) {
			AppUnifilter_Query {
				getFieldValues (
	        table: $table,
	        field: $field,
	        encodedQuery: $encodedQuery,
	        sort: $sort,
	        nextRecordIdx: $nextRecordIdx,
	        maxNbRecords: $maxNbRecords,
	        searchTerm: $search
	      ) {
	        nextRecordIdx
	        values {
	            label
	            id: value
	        }
	      }
	    }
		}
	`,
	getData (data) {
		if (data && data.AppUnifilter_Query && data.AppUnifilter_Query.getFieldValues && data.AppUnifilter_Query.getFieldValues.values) {
			return data.AppUnifilter_Query.getFieldValues.values;
		}

		return null;
	},
};
