export default function searchItems (items, term = null) {
	if (term === null) {
		return items;
	}

	const isGroupedData = Array.isArray(items) && items.length && Array.isArray(items[0].items);
	const query = term.toLowerCase();

	// Filter plain list of items
	if (!isGroupedData) {
		return items.filter(({ label }) => {
			return label.toLowerCase().includes(query);
		});
	}

	return items.reduce((prev, curr) => {
		const hasMatch = curr.label.toLowerCase().includes(query);

		if (hasMatch) {
			// Match in top level label means match that entire group is returned as matched
			prev.push(curr);
		} else {
			// There are items array in it in group, filter
			const innerItems = searchItems(curr.items, query);

			if (innerItems.length > 0) {
				prev.push({
					...curr,
					items: innerItems,
				});
			}
		}

		return prev;
	}, []);
}
