import differenceWith from 'lodash/differenceWith';
import isEqual from 'lodash/isEqual';

/**
 * filterItems currently only works with simple list of items. If grouped data is used in multiselect mode
 * then filterItems also needs to support filtering in groups as well. Required changes are similar to code in
 * getSearchFilter.
 * @param items1 {Array<{ label: string, id: string }>}
 * @param items2 {Array<{ label: string, id: string }>}
 */
const filterItems = (items1, items2) => differenceWith(items1, items2, isEqual);

export default filterItems;
