import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';

import {
	GET_CHOICES, GET_VALUES,
} from './queries';
import {
	BEFORE_REQUEST_DATA,
	RECEIVE_CHOICES_DATA,
	RECEIVE_DATA_ERROR,
	RECEIVE_VALUES_DATA,
} from './constants';

export const requestChoicesData = createGraphQLEffect(GET_CHOICES.query, {
	variableList: ['table', 'field'],
	startActionType: BEFORE_REQUEST_DATA,
	successActionType: RECEIVE_CHOICES_DATA,
	errorActionType: RECEIVE_DATA_ERROR,
});

export const requestValuesData = createGraphQLEffect(GET_VALUES.query, {
	variableList: ['table', 'field', 'sort', 'search', 'encodedQuery'],
	startActionType: BEFORE_REQUEST_DATA,
	successActionType: RECEIVE_VALUES_DATA,
	errorActionType: RECEIVE_DATA_ERROR, // TODO: check if request fails with 500 then error payload is JSON.parse error because server response is not JSON
});
