import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { createRef, Fragment } from '@servicenow/ui-renderer-snabbdom';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';

import styles from './filter-config-panel.scss';
import { getCount } from './countApi';

const tableUrl = '/api/now/doc/table/schema';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const addDatasourceBtnText = t('Add');
const configureDatasourceBtnText = t('Configure values');
const addDatasourceBtnAriaConfig = { 'aria-label': t('Add datasource') };

const sortingItems = [
	{ id: 'ASC', label: t('Ascending'), icon: 'sort-ascending-fill' },
	{ id: 'DESC', label: t('Descending'), icon: 'sort-descending-fill' },
];

const pillar = ({ table, field }) => {
	const id = `${table.id}.${field.id}`;
	const actions = [
		{
			icon: 'pencil-outline',
			id: 'edit',
			label: t('Edit datasource'),
		},
		{
			icon: 'trash-outline',
			id,
			label: t('Delete datasource'),
		},
	];
	const pillarRef = createRef();
	const appendToPayload = {
		name: 'tablefield',
		ref: pillarRef,
		table: {
			id: table.id,
			label: `${table.label} [${table.id}]`,
		},
		field: {
			label: field.label,
			value: field.id,
		},
	};

	return (
		<sn-par-pillar
			className="pillar"
			key={id}
			ref={pillarRef}
			modifierLabel={table.label}
			append-to-payload={appendToPayload}
			actions={actions}
		>
			{field.label}
		</sn-par-pillar>
	);
};

const getSourceInfo = (selectedTable, selectedField) => {
	if (selectedField.type === 'choice') {
		return {
			table: 'sys_choice',
			label: selectedField.choiceLabel,
			title: `${selectedField.choiceLabel} [sys_choice] - ${selectedTable.count}`,
			type: selectedField.type,
		};
	}

	if (selectedField.type === 'reference') {
		return {
			table: selectedField.reference,
			label: selectedField.referenceLabel,
			title: `${selectedField.referenceLabel} [${selectedField.reference}] - ${selectedTable.count}`,
			type: selectedField.type,
		};
	}

	// Manually checked with glide/src/com/glide/db/IDBTypes.java
	if (/date/.test(selectedField.type)) {
		return {
			table: null,
			label: null,
			title: t('Date range'),
			type: selectedField.type,
		};
	}

	// TODO: These types of fields are not supported. What kind of message should it render?
	//  For now just format title friendly instead of "Date range" for all the fields.

	const title = selectedField.reference
		? `${selectedField.referenceLabel || selectedField.label} [${selectedField.reference}]`
		: selectedField.label;

	return {
		table: null,
		label: null,
		title,
	};
};

const view = (state, { dispatch, updateState }) => {
	const {
		targetRef,
		popoverOpened,
		modalOpened,
		tables,
		editTable,
		editField,
		errors,
		properties,
		encodedQuery,
	} = state;
	const filterProperties = JSON.parse(properties.filterProperties || '{}');
	const {
		tablefields = [],
		listMode = 'multi',
		emptyOption = null,
		sort = 'ASC',
		defaults = [],
		booleanLabels = {},
	} = filterProperties;
	const selectedTable = (tablefields && tablefields[0] && tablefields[0].table) || {};
	const selectedField = (tablefields && tablefields[0] && tablefields[0].field) || {};
	const sourceInfo = getSourceInfo(selectedTable, selectedField);

	const handlePopoverOpen = ({ currentTarget }) => {
		updateState({
			targetRef: currentTarget,
			popoverOpened: true,
		});
	};
	const handlePopoverClose = () => {
		updateState({
			popoverOpened: false,
			editTable: {},
			editField: {},
		});
	};
	const handlePopoverSave = (tableFieldData) => {
		dispatch('ADD_TABLEFIELD#ADD', tableFieldData);
		updateState({
			popoverOpened: false,
			editTable: {},
			editField: {},
		});
	};

	const isDaterange = /date/i.test(selectedField.type);
	const isBoolean = selectedField.type === 'boolean';
	const isChoice = selectedField.type === 'choice';
	const showAdvancedOptions = !isDaterange;
	const isTableSelected = Boolean(selectedTable.id);
	const showConfigureDateseource = !isDaterange && !isBoolean && !isChoice;

	return (
		<section className="filterProperties">
			<now-accordion
				heading-level="2"
				trigger-icon={{ type: 'chevron', position: 'end' }}
			>
				{/* Data sources */}
				<now-accordion-item
					key="data-section"
					header={{
						label: t('Data'),
						variant: 'primary',
						weight: 'bold',
						size: 'sm',
					}}
					expanded
				>
					<div slot="content">
						<div className="pillars">
							{isTableSelected ? (
								<Fragment>
									{tablefields.map(pillar)}
									<div className="data-section-bottom-buttons">
										<div className="btn-conf-values">
											{showConfigureDateseource ? (
												<now-button
													on-click={() => updateState({ modalOpened: true })}
													icon="sliders-outline"
													size="md"
													label={configureDatasourceBtnText}
													tooltipContent={configureDatasourceBtnText}
													configAria={{
														'aria-label': t('Advanced datasource configuration'),
													}}
												/>
											) : ''}
										</div>
										<now-button-bare
											className="btn-open-popover"
											on-click={handlePopoverOpen}
											configAria={addDatasourceBtnAriaConfig}
											size="md"
											icon-start="plus-outline"
											label={`${addDatasourceBtnText}\xa0`}
											tooltipContent={addDatasourceBtnText}
										/>
									</div>
									{modalOpened ? (
										<par-filter-config-advanced-options
											source={sourceInfo}
											encodedQuery={encodedQuery}
											emptyOption={emptyOption}
										/>
									) : ''}
								</Fragment>
							) : (
								<div className="data-section-empty-datasource">
									<p className="info-line1">
										{t('Data to filter')}
									</p>
									<p className="info-line2">
										{t('Define the source tables and the fields you want to use for this filter.')}
									</p>
									<now-button
										className="btn-open-popover"
										variant="primary"
										size="md"
										on-click={handlePopoverOpen}
										configAria={addDatasourceBtnAriaConfig}
										icon="plus-outline"
										label={`${addDatasourceBtnText}\xa0`}
									/>
								</div>
							)}

							<par-filter-config-tablefield-popover
								tables={tables}
								errors={errors}
								opened={popoverOpened}
								targetRef={targetRef}
								editTable={editTable}
								editField={editField}
								onClose={handlePopoverClose}
								onSave={handlePopoverSave}
							/>
						</div>
					</div>
				</now-accordion-item>

				{/* Default values */}
				{isTableSelected ? (
					<now-accordion-item
						expanded
						key="default-values-section"
						header={{
							label: t('Default values'),
							variant: 'primary',
							weight: 'bold',
							size: 'sm',
						}}
					>
						<par-filter-config-default-values
							slot="content"
							className="default-values-section"
							defaults={defaults}
							config={{
								table: selectedTable.id,
								field: selectedField.id,
								fieldType: selectedField.type,
								encodedQuery,
								booleanLabels,
							}}
							single={isBoolean || listMode === 'single'}
						/>
					</now-accordion-item>
				) : ''}

				{/* Advanced options */}
				{isTableSelected && showAdvancedOptions ? (
					<now-accordion-item
						key="advanced-values-section"
						header={{
							label: t('Advanced options'),
							variant: 'primary',
							weight: 'bold',
							size: 'sm',
						}}
						expanded
					>
						<div slot="content" className="advanced-options-section">
							{/* Advanced options for choice and reference fields */}
							{!isBoolean ? (
								<Fragment>
									<par-filter-config-toggle
										appendToPayload={{ toggleType: 'listMode' }}
										label={t('Allow multiple element selection')}
										checked={listMode === 'multi'}
									/>

									<div className="sorting-option">
										<p className="sorting-label">{t('Sort direction')}</p>
										<now-dropdown
											items={sortingItems}
											selectedItems={[sort]}
										/>
									</div>
								</Fragment>
							) : null}

							{/* Advanced options for boolean fields */}
							{isBoolean ? (
								<par-filter-config-boolean-labels labels={booleanLabels} />
							) : null}
						</div>
					</now-accordion-item>
				) : ''}
			</now-accordion>
		</section>
	);
};

createCustomElement('now-filters-config-panel', {
	view,
	styles,
	properties: {
		filterProperties: {},
		filterConfigurations: {},
	},
	initialState: {
		tables: [],
		errors: {},
		popoverOpened: false,
		modalOpened: false,
		targetRef: {},
		editTable: {},
		editField: {},
		encodedQuery: null,
		emptyOption: null,
	},
	transformState (state) {
		const { encodedQuery } = state;

		if (encodedQuery !== null) {
			return state;
		}

		const filterProperties = JSON.parse(state.properties.filterProperties || '{}');

		return {
			...state,
			encodedQuery: filterProperties.encodedQuery || '',
			emptyOption: filterProperties.emptyOption,
		};
	},
	eventHandlers: [
		{
			events: ['resize'],
			modifier: { name: 'debounce', delay: 300 },
			effect ({ updateState }) {
				updateState({
					popoverOpened: false,
				});
			},
			target: window,
		},
	],
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch('TABLES_LOAD', {
				sysparm_allow_public: true,
				sysparm_allow_sys_table: true,
			});
		},

		TABLES_LOAD: createHttpEffect(tableUrl, {
			method: 'GET',
			queryParams: ['sysparm_allow_public', 'sysparm_allow_sys_table'],
			successActionType: 'TABLES_DATA',
			errorActionType: 'TABLES_ERROR',
			// for debugging
			batch: false,
		}),
		TABLES_DATA: ({ action, updateState }) => {
			const tables = (action.payload && action.payload.result) || [];

			updateState({
				tables,
				errors: {},
			});
		},
		TABLES_ERROR: ({ updateState }) => {
			updateState({
				errors: {
					tables: 'Error loading tables',
				},
			});
		},

		'ADD_TABLEFIELD#ADD': async ({ action, state, dispatch }) => {
			const { payload: { table, field } } = action;
			const { editTable } = state;
			const filterProperties = JSON.parse(state.properties.filterProperties || '{}');
			const tablefields = (filterProperties && filterProperties.tablefields) || [];
			const index = tablefields.findIndex((tablefieldItem) => tablefieldItem.table.id === (editTable.id || table.id));
			const oldField = tablefields[0] && tablefields[0].field.id;

			if (index > -1) {
				tablefields[index] = { table, field };
			} else {
				tablefields.push({ table, field });
			}

			// first tablefield was just added
			if (tablefields.length === 1) {
				if (tablefields[0].field.type === 'choice') {
					tablefields[0].table.count = (tablefields[0].field.choices && tablefields[0].field.choices.length) || 0;
				} else if (tablefields[0].field.type === 'reference') {
					const count = await getCount(
						tablefields[0].field.reference,
						tablefields[0].field.referenceField
					);

					tablefields[0].table.count = count;
				}
			}

			const defaults = field.id !== oldField ? [] : filterProperties.defaults;
			const booleanLabels = field.id !== oldField ? {} : filterProperties.booleanLabels;

			dispatch('UPDATE_CHANGE_PROPERTIES', {
				...filterProperties,
				tablefields,
				defaults,
				booleanLabels,
			});
		},

		'SN_PAR_PILLAR#ACTION_CLICKED': async ({
			action, state, dispatch, updateState,
		}) => {
			const payload = action.payload;
			if (payload.name !== 'tablefield') { return; }

			// Edit table/field
			if (payload.ref && payload.id === 'edit') {
				const { table, field } = payload;

				updateState({
					targetRef: payload.ref.current,
					popoverOpened: true,
					editTable: table,
					editField: field,
				});
				return;
			}

			const filterProperties = JSON.parse(state.properties.filterProperties || '{}');
			const tablefields = (filterProperties.tablefields || [])
				.filter(({ table, field }) => `${table.id}.${field.id}` !== payload.id);
			let extraProps = {};

			// first tablefield was just removed
			if (tablefields.length > 0 && !tablefields[0].table.count) {
				if (tablefields[0].field.type === 'choice') {
					tablefields[0].table.count = tablefields[0].field.choices.length;
				} else if (tablefields[0].field.type === 'reference') {
					const count = await getCount(
						tablefields[0].field.reference,
						tablefields[0].field.referenceField
					);

					tablefields[0].table.count = count;
				}

				// need to clear these also when first table.field is removed.
				extraProps = {
					encodedQuery: null,
					emptyOption: null,
					listMode: 'multi',
					defaults: [],
					booleanLabels: {},
				};
			}

			// Need to clear emptyOption, listMode and encodedQuery if the last datasource was removed
			if (tablefields.length === 0) {
				extraProps = {
					encodedQuery: null,
					emptyOption: null,
					listMode: 'multi',
					sort: 'ASC',
					defaults: [],
					booleanLabels: {},
				};
			}

			dispatch('UPDATE_CHANGE_PROPERTIES', {
				...filterProperties,
				...extraProps,
				tablefields,
			});
		},

		// Advanced options modal actions
		'PAR_FILTER_ADVANCED_OPTIONS#CLOSE' ({ updateState }) {
			updateState({
				modalOpened: false,
			});
		},
		'PAR_FILTER_ADVANCED_OPTIONS#SAVE' ({
			properties, action, updateState, dispatch,
		}) {
			const { payload: { encodedQuery, emptyOption } } = action;
			const filterProperties = JSON.parse(properties.filterProperties || '{}');
			const resetDefaults = encodedQuery !== filterProperties.encodedQuery;

			updateState({
				emptyOption,
				encodedQuery,
				modalOpened: false,
			});
			dispatch('UPDATE_CHANGE_PROPERTIES', {
				...filterProperties,
				...(resetDefaults && { defaults: [] }),
				encodedQuery,
				emptyOption,
			});
		},

		'PAR_FILTER_TOGGLE#CHECKED_SET' ({ action, properties, dispatch }) {
			if (action.payload.toggleType === 'listMode') {
				const filterProperties = JSON.parse(properties.filterProperties || '{}');
				const listMode = action.payload.value ? 'multi' : 'single';
				const defaults = filterProperties.defaults.length
					? filterProperties.defaults.slice(0, 1)
					: [];

				dispatch('UPDATE_CHANGE_PROPERTIES', {
					...filterProperties,
					listMode,
					defaults,
				});
			}
			action.stopPropagation();
		},

		'NOW_DROPDOWN#SELECTED_ITEMS_SET' ({ action, properties, dispatch }) {
			const { payload: { value } } = action;
			const filterProperties = JSON.parse(properties.filterProperties || '{}');

			dispatch('UPDATE_CHANGE_PROPERTIES', {
				...filterProperties,
				sort: value[0],
			});

			action.stopPropagation();
		},

		'PAR_FILTER_CONFIG_DEFAULT_VALUES#ITEMS_SET' ({ properties, action, dispatch }) {
			const filterProperties = JSON.parse(properties.filterProperties || '{}');
			const { payload: { value } } = action;
			const defaults = (value.length && value[0].id !== null) || (value.start && value.end) ? value : [];

			dispatch('UPDATE_CHANGE_PROPERTIES', {
				...filterProperties,
				defaults,
			});
		},

		'PAR_FILTER_CONFIG_BOOLEAN_LABELS#LABELS_SET' ({ action, properties, dispatch }) {
			const filterProperties = JSON.parse(properties.filterProperties || '{}');
			const { payload: { label, type } } = action;
			const booleanLabels = {
				...filterProperties.booleanLabels,
				[type]: label,
			};

			if (label.length === 0) {
				delete booleanLabels[type];
			}

			dispatch('UPDATE_CHANGE_PROPERTIES', {
				...filterProperties,
				booleanLabels,
			});
		},

		'UPDATE_CHANGE_PROPERTIES' ({ action: { payload }, properties, dispatch }) {
			// needed for updating UIB
			dispatch('SN_UIB_ELEMENT_CONFIGURATION_PANEL#PROPERTY_CHANGED_IMMEDIATE', {
				propertyId: 'filterProperties',
				value: JSON.stringify(payload),
			});

			if (properties.filterConfigurations !== '@state.parFilters') {
				dispatch('SN_UIB_ELEMENT_CONFIGURATION_PANEL#PROPERTY_CHANGED_IMMEDIATE', {
					propertyId: 'filterConfigurations',
					value: '@state.parFilters',
				});
			}
		},

	},
});
