/**
 * As there are no known good reliable way in runtime to get version of a component (kudos to TECTONIC team)
 * this is a custom manually crafted version timestamp like in prehistoric times.
 */
try {
	// eslint-disable-next-line global-require,import/no-unresolved
	const versionInfo = require('../tools/current-version-info');

	window.PAR_COMPONENTS_INFO = window.PAR_COMPONENTS_INFO || {};
	window.PAR_COMPONENTS_INFO.UnifiedFilters = versionInfo;
} catch (ex) {
	/** Suppress module not found error in case version file was not generated during the build */
}
