import keyBindingBehavior from '@servicenow/behavior-key-binding';
import rtlBehavior from '@servicenow/behavior-rtl';
import focusBehavior from '@servicenow/behavior-focus';

import { NOW_FILTER__TOGGLED } from './constants';

export default [
	{
		behavior: keyBindingBehavior,
		options: {
			keyBindings: {
				open: ['Enter', ' '],
				close: ['Escape'],
			},
			handlers: {
				open ({ opened }, event, dispatch) {
					if (!opened) {
						dispatch(NOW_FILTER__TOGGLED, { value: true });
						event.preventDefault();
					}
				},
				close (host, event, dispatch) {
					dispatch(NOW_FILTER__TOGGLED, { value: false });
					setTimeout(() => host.focus(), 0);
				},
			},
		},
	},
	{ behavior: rtlBehavior },
	{ behavior: focusBehavior },
];
