import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';

import { PAR_FILTER_DATERANGE_PANEL__VALUE_SET } from './daterange-panel';
import { NOW_FILTER__SET_DIRTY } from '../constants';
import { RadioGroup } from './radio-group';
import styles from './daterange-content.scss';
import {
	today,
	toOption,
	toStart,
	matchRange,
} from './helpers';

const view = (
	{ properties, start, end },
	{ dispatch }
) => {
	const { presets } = properties;

	return (
		<div className="daterange-container">
			<RadioGroup
				options={presets.map(toOption)}
				checkedOption={matchRange(presets, start, end)}
				dispatch={dispatch}
			/>
			<par-filter-daterange-panel
				start={start}
				end={end}
			/>
			<div className="custom-inputs">
				<now-input
					data-testCustomDaterangeField
					type="text"
					value={start || ''}
					label={t('Start')}
					pattern="(19|20)[0-9]{2}-[0-9]{2}-[0-9]{2}"
					append-to-payload={{
						type: 'start',
						end,
					}}
				/>
				<span className="separator"></span>
				<now-input
					data-testCustomDaterangeField
					type="text"
					value={end || ''}
					label={t('End')}
					pattern="(19|20)[0-9]{2}-[0-9]{2}-[0-9]{2}"
					append-to-payload={{
						type: 'end',
						start,
					}}
				/>
			</div>
		</div>
	);
};

createCustomElement('par-filter-daterange-content', {
	properties: {
		presets: {
			default: [{
				label: t('Last 7 Days'),
				range: '-7 days',
			}, {
				label: t('Last Month'),
				range: '-1 months',
			}, {
				label: t('Last 3 Months'),
				range: '-3 months',
			}, {
				label: t('Last 6 Months'),
				range: '-6 months',
			}, {
				label: t('Year To Date'),
				range: 'year to date',
			}, {
				label: t('Last Year'),
				range: '-1 year',
			}, {
				label: t('Custom'),
			}],
		},
		start: {
			default: today,
		},
		end: {
			default: today,
		},
	},
	transformState: (state) => {
		if (state.start || state.end) {
			return state;
		}
		return {
			...state,
			start: state.properties.start,
			end: state.properties.end,
		};
	},
	view,
	styles,
	actionHandlers: {
		[PAR_FILTER_DATERANGE_PANEL__VALUE_SET] ({ action, dispatch, updateState }) {
			const { payload: { start, end } } = action;

			updateState({
				start,
				end,
			});

			// Only allow both fields filled or both empty (to reset the filter).
			if ((start && end) || (!start && !end)) {
				dispatch(NOW_FILTER__SET_DIRTY, {
					dirty: true,
					dirtyBag: {
						start,
						end,
					},
				});
			}
		},
		'RADIO_GROUP#CHECKED': ({
			action, updateState, dispatch,
		}) => {
			const {
				payload: {
					value,
				},
			} = action;

			const start = toStart(value);
			const end = today;

			updateState({
				start,
				end,
			});

			dispatch(NOW_FILTER__SET_DIRTY, {
				dirty: true,
				dirtyBag: {
					start,
					end,
				},
			});
		},
		'NOW_INPUT#VALUE_SET': ({
			action, updateState, dispatch,
		}) => {
			const {
				payload: {
					value,
					type,
				},
			} = action;
			let {
				payload: {
					start,
					end,
				},
			} = action;

			if (type === 'start') {
				start = value;
			}
			if (type === 'end') {
				end = value;
			}

			updateState({
				start,
				end,
			});

			// Only allow both fields filled or both empty (to reset the filter).
			if ((start && end) || (!start && !end)) {
				dispatch(NOW_FILTER__SET_DIRTY, {
					dirty: true,
					dirtyBag: {
						start,
						end,
					},
				});
			}
		},
	},
});
