import { t } from 'sn-translate';
import { actionTypes } from '@servicenow/ui-core';

import effectProxy from '../helpers/effectProxy';

import { GET_CHOICES, GET_VALUES } from './queries';
import {
	BEFORE_REQUEST_DATA,
	REQUEST_DATA,
	RECEIVE_DATA,
	REQUEST_CHOICES_DATA,
	RECEIVE_CHOICES_DATA,
	RECEIVE_DATA_ERROR,
	REQUEST_VALUES_DATA,
	RECEIVE_VALUES_DATA,
	REQUEST_BOOLEAN_DATA,
} from './constants';
import {
	requestChoicesData,
	requestValuesData,
} from './actions';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const requestDataActions = {
	choice: REQUEST_CHOICES_DATA,
	reference: REQUEST_VALUES_DATA,
	boolean: REQUEST_BOOLEAN_DATA,
};

const initialState = {
	// copies of props which was used last time to query the data
	requestedPropsJSON: undefined,
};

const properties = {
	table: {},
	field: {},
	fieldType: {},
	sort: {},
	search: {},
	encodedQuery: {},
	/**
	 * emptyOption can be either null or a string. If it's a string it's appended to dataResponse available items.
	 */
	emptyOption: {},
	booleanLabels: {},

	resultData: { selectable: true },
	loading: { selectable: true, default: false },
	errorMessage: { selectable: true, default: '' },
};

function getRequestParams ({
	table, field, fieldType, search, sort, encodedQuery, emptyOption, booleanLabels,
}) {
	return {
		table, field, fieldType, search, sort, encodedQuery, emptyOption, booleanLabels,
	};
}

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED] ({ dispatch }) {
		dispatch(REQUEST_DATA, {});
	},

	[COMPONENT_PROPERTY_CHANGED]: {
		modifier: { name: 'debounce', delay: 30 },
		effect ({ state, properties, dispatch }) {
			const requestedProps = getRequestParams(properties);
			if (JSON.stringify(requestedProps) !== state.requestedPropsJSON) {
				dispatch(REQUEST_DATA, { refetch: true });
			}
		},
	},

	[BEFORE_REQUEST_DATA] ({ properties, updateProperties, updateState }) {
		updateProperties({
			loading: true,
			resultData: undefined,
			errorMessage: '',
		});
		updateState({
			requestedPropsJSON: JSON.stringify(getRequestParams(properties)),
		});
	},

	[REQUEST_DATA] ({ action, properties, dispatch }) {
		const { payload: { refetch } } = action;
		const { fieldType, resultData	} = properties;
		const requestAction = requestDataActions[fieldType];

		if (!requestAction) {
			throw new Error(`Filter data provider do not support field type: ${fieldType}`);
		}

		if (!resultData || refetch) {
			const requestParams = getRequestParams(properties);
			dispatch(requestAction, requestParams);
		}
	},

	// boolean actions
	[REQUEST_BOOLEAN_DATA] ({ properties, dispatch }) {
		const { booleanLabels } = properties;
		const {
			trueLabel = t('True'),
			falseLabel = t('False'),
		} = booleanLabels;
		const dataItems = [
			{ id: 'true', label: trueLabel },
			{ id: 'false', label: falseLabel },
		];

		dispatch(BEFORE_REQUEST_DATA);
		dispatch(RECEIVE_DATA, { dataItems });
	},

	// choices actions
	[REQUEST_CHOICES_DATA]: effectProxy(requestChoicesData),

	[RECEIVE_CHOICES_DATA] ({ action: { payload }, dispatch }) {
		const dataItems = GET_CHOICES.getData(payload.data);
		if (dataItems) {
			dispatch(RECEIVE_DATA, { dataItems });
		} else {
			// const [ error ] = payload.errors
			// dispatch(RECEIVE_DATA_ERROR, new Error(t('Could not fetch choices.')));

			// STRY51451958: it was decided not to show UI errors until Rome until we find a way to prevent selection
			// of incompatible encodedQuery for datasource
			dispatch(RECEIVE_DATA, { dataItems: [] });
		}
	},

	// references actions
	[REQUEST_VALUES_DATA]: effectProxy(requestValuesData),

	[RECEIVE_VALUES_DATA] ({ action, properties, dispatch }) {
		const { payload } = action;
		const { emptyOption } = properties;
		const data = GET_VALUES.getData(payload.data);

		if (data) {
			const dataItems = emptyOption ? data.concat({ label: emptyOption, id: '' }) : data;
			dispatch(RECEIVE_DATA, { dataItems });
		} else {
			// dispatch(RECEIVE_DATA_ERROR, new Error(t('Could not fetch values.')));
			dispatch(RECEIVE_DATA, { dataItems: [] });
		}
	},

	[RECEIVE_DATA_ERROR] ({ action: { payload }, dispatch }) {
		dispatch(RECEIVE_DATA, { error: payload });
	},

	[RECEIVE_DATA] ({ action: { payload }, updateProperties }) {
		updateProperties({
			loading: false,
			errorMessage: payload.error ? (payload.error.message || 'Error occurred while loading filter data.') : undefined,
			resultData: payload.error ? null : payload.dataItems,
		});
	},
};

export const filterDataProvider = {
	initialState,
	properties,
	actionHandlers,
};
