/**
 * Based on https://www.w3.org/Consortium/Legal/2015/copyright-software-and-document
 */
export class VanillaListbox {
	constructor ({ multiple = true, listbox, onChange = () => {} }) {
		this.listboxNode = listbox;
		// TODO: important to have a live collection here so can't use querySelectorAll('[role=option]') reliably?
		// this.options = this.listboxNode.querySelectorAll('[role=option]');
		this.options = this.listboxNode.getElementsByClassName('option');
		this.onChange = onChange;
		this.shiftPressed = false;
		this.multiple = multiple;

		const activeDescendantId = this.listboxNode.getAttribute('aria-activedescendant');
		this.activeDescendant = activeDescendantId ? this.listboxNode.querySelector(activeDescendantId) : null;

		this.initEvents();
	}

	initEvents () {
		this.listboxNode.addEventListener('blur', this.handleBlur.bind(this));
		this.listboxNode.addEventListener('focus', this.handleFocus.bind(this));
		this.listboxNode.addEventListener('keydown', this.handleKeyDown.bind(this));
		this.listboxNode.addEventListener('keyup', this.handleKeyUp.bind(this));
		this.listboxNode.addEventListener('click', this.handleClick.bind(this));
		this.listboxNode.addEventListener('mousedown', this.handleMouseDown.bind(this));
	}

	handleMouseDown (e) {
		if (this.shiftPressed) {
			e.preventDefault();
		}
	}

	handleBlur () {
		const focused = this.listboxNode.querySelector('.focused');

		if (focused) {
			focused.classList.remove('focused');
			this.clearActiveDescendant();
		}
	}

	handleFocus () {
		setTimeout(() => {
			if (!this.activeDescendant) {
				const firstItem = this.options[0];
				if (firstItem) {
					this.focusItem(firstItem);
				}
			}
		}, 100);
	}

	handleKeyUp ({ key }) {
		if (key === 'Shift') {
			this.shiftPressed = false;
		}
	}

	handleKeyDown (e) {
		const { key, metaKey, shiftKey } = e;
		const lastActive = this.activeDescendant;
		const firstItem = this.options[0];
		const item = this.activeDescendant || firstItem;
		let nextItem = null;

		if (!item) {
			return;
		}

		switch (key) {
		case 'Shift':
			this.shiftPressed = true;
			e.preventDefault();
			break;

		case 'ArrowUp':
		case 'ArrowDown':
			e.preventDefault();

			if (!this.activeDescendant) {
				// focus first option if no option was previously focused, and perform no other actions
				this.focusItem(firstItem);

				if (shiftKey) {
					this.toggleSelectItem(firstItem);
				}
				break;
			}

			if (key === 'ArrowUp') {
				nextItem = this.findPreviousOption(item);
			} else {
				nextItem = this.findNextOption(item);
			}

			if (nextItem) {
				if (shiftKey) {
					if ((!this.isSelected(item) && !this.isSelected(nextItem)) || (this.isSelected(item) && this.isSelected(nextItem))) {
						this.toggleSelectItem(item);
					} else {
						this.toggleSelectItem(nextItem);
						this.focusItem(nextItem);
					}
				} else {
					this.focusItem(nextItem);
				}
			}

			break;

		case ' ':
			e.preventDefault();
			this.toggleSelectItem(item);
			break;

		case 'a':
			if (metaKey && this.multiple) {
				e.preventDefault();
				this.selectAllItems();
			}
			break;

		default:
			break;
		}

		if (this.activeDescendant !== lastActive) {
			this.updateScroll();
		}
	}

	findNextOption (currentOption) {
		const currentOptionIndex = Number(currentOption.dataset.index);

		if (currentOptionIndex < this.options.length - 1) {
			return this.options[currentOptionIndex + 1];
		}

		return null;
	}

	findPreviousOption (currentOption) {
		const currentOptionIndex = Number(currentOption.dataset.index);

		if (currentOptionIndex > 0) {
			return this.options[currentOptionIndex - 1];
		}

		return null;
	}

	handleClick ({ target, shiftKey }) {
		if (target.getAttribute('role') === 'option') {
			if (shiftKey && this.multiple) {
				const index1 = Number(this.activeDescendant.dataset.index);
				const index2 = Number(target.dataset.index);
				const min = Math.min(index1, index2);

				this.selectRange(min, index1 + index2 - min);
			} else {
				this.focusItem(target);
				this.toggleSelectItem(target);
				this.updateScroll();
			}
		}
	}

	isSelected (element) {
		return element.getAttribute('aria-selected') === 'true';
	}

	selectRange (from, to) {
		for (let i = from; i <= to; i += 1) {
			this.options[i].setAttribute('aria-selected', 'true');
		}

		this.focusItem(this.options[to]);
		this.onChange(this.getSelectedItems());
	}

	toggleSelectItem (element, state) {
		let selected = state;

		if (selected === undefined) {
			selected = this.isSelected(element) ? 'false' : 'true';
		}

		if (selected === 'true' && !this.multiple) {
			this.unselectAllItems();
		}

		element.setAttribute('aria-selected', selected);
		this.onChange(this.getSelectedItems());
	}

	selectAllItems () {
		this.selectRange(0, this.options.length - 1);
	}

	unselectAllItems () {
		for (let i = 0; i < this.options.length; i += 1) {
			this.options[i].setAttribute('aria-selected', 'false');
		}
	}

	expandGroup (element) {
		if (element.parentNode.classList.contains('collapsed')) {
			element.parentNode.classList.remove('collapsed');
		}
	}

	getSelectedItems () {
		const selected = [];

		for (let i = 0; i < this.options.length; i += 1) {
			const element = this.options[i];

			if (this.isSelected(element)) {
				selected.push({
					label: element.textContent,
					id: element.dataset.id !== undefined ? element.dataset.id : '',
				});
			}
		}

		return selected;
	}

	defocusItem (element) {
		if (!element) {
			return;
		}

		element.classList.remove('focused');
	}

	focusItem (element) {
		this.defocusItem(this.activeDescendant);
		element.classList.add('focused');
		this.expandGroup(element);
		this.listboxNode.setAttribute('aria-activedescendant', element.id);
		this.activeDescendant = element;
	}

	updateScroll () {
		const selectedOption = this.activeDescendant;

		if (selectedOption && this.listboxNode.scrollHeight > this.listboxNode.clientHeight) {
			const scrollBottom = this.listboxNode.clientHeight + this.listboxNode.scrollTop;
			const elementBottom = selectedOption.offsetTop + selectedOption.offsetHeight;

			if (elementBottom > scrollBottom) {
				this.listboxNode.scrollTop = elementBottom - this.listboxNode.clientHeight;
			} else if (selectedOption.offsetTop < this.listboxNode.scrollTop) {
				this.listboxNode.scrollTop = selectedOption.offsetTop;
			}
		}
	}

	clearActiveDescendant () {
		this.activeDescendant = null;
		this.listboxNode.setAttribute('aria-activedescendant', null);
	}
}
