import '@servicenow/now-dot-walk';
import { t } from 'sn-translate';
import styles from './dot-walk.scss';

function getFieldsPathInfo (paths) {
	return {
		value: paths.map(({ field }) => field).join('.'),
		label: paths.map(({ displayValue }) => displayValue).join('  '),
	};
}

/**
 * This dirty hack is needed because dot-walk components doesn't stop propagation of click events from it
 * as the result if dot-walk component is used inside now-popover clicking search filter will close popover.
 * Can be removed once/if DEF0132546 is fixed.
 *
 * Another hack is to focus search field automatically to make UX simpler.
 */
function stopClickPropagation (node) {
	setTimeout(() => {
		if (node.nowHoistAppendTo) {
			const waitForInput = () => {
				setTimeout(() => {
					// seismic-hoist and now-dot-walk are guarantied to exist in this structure so chaining is safe
					const input = node.nowHoistAppendTo
						.querySelector('seismic-hoist').shadowRoot
						.querySelector('now-dot-walk').shadowRoot
						.querySelector('input');

					if (!input) {
						waitForInput();
					} else {
						input.focus();
					}
				}, 50);
			};

			waitForInput();
			node.nowHoistAppendTo.addEventListener('click', (e) => e.stopPropagation());
		}
	}, 100);
}

export const filtersDotWalkView = ({ opened, selectedPath, properties }, { updateState }) => {
	const { table, selectedLabel, placeholder } = properties;
	const disabled = !table;

	const isSupportedType = (type) => type === undefined ||
		/date/.test(type) ||
		type in {
			choice: true,
			reference: true,
			boolean: true,
		};

	const mergeObjects = [
		(obj, sub) => ({
			...obj,
			...sub,
		}),
		{},
	];

	const transformMetadata = (metadata) => Object
		.keys(metadata)
		.map((table) => ({
			[table]: Object
				.keys(metadata[table])
				.map((field) => ({
					[field]: {
						...metadata[table][field],
						disabled: !isSupportedType(metadata[table][field].type || metadata[table][field].fieldMetadata.type),
					},
				}))
				.reduce(...mergeObjects),
		}))
		.reduce(...mergeObjects);

	return (
		<div className="container">
			<label id="dot-walk-label" className="dot-walk-label">
				{t('Field')}
			</label>

			<now-popover
				hideTail
				manageOpened
				opened={opened}
				positions={[
					{ target: 'bottom-start', content: 'top-start' },
					{ target: 'bottom-end', content: 'top-end' },
					{ target: 'top-start', content: 'bottom-start' },
					{ target: 'top-end', content: 'bottom-end' },
				]}
			>
				<button
					slot="trigger"
					type="button"
					class={{ 'btn-trigger': true, 'is-open': opened, disabled }}
					aria-haspopup="listbox"
					aria-labelledby="dot-walk-label trigger-label"
					aria-expanded={String(opened)}
					on-click={(e) => {
						if (e.detail > 1) {
							return;
						}
						updateState({ opened: !opened });
					}}
					on-keydown={(e) => {
						if (e.key === 'ArrowDown') {
							updateState({ opened: true });
						}
					}}
					disabled={disabled}
				>
					<span
						id="trigger-label"
						class={{ 'trigger-label': true, placeholder: !selectedLabel }}
					>
						{selectedLabel || placeholder}
					</span>
					<now-icon icon="caret-down-outline" />
				</button>

				{opened && table ? (
					<now-dot-walk-connected
						hook-insert={({ elm }) => stopClickPropagation(elm)}
						slot="content"
						tableName={table}
						dotWalkPath={selectedPath}
						dotWalkEnabledFieldTypes={['reference']}
						transformMetadata={transformMetadata}
					/>
				) : null}
			</now-popover>
		</div>
	);
};

export const filtersDotWalk = {
	initialState: {
		opened: false,
		selectedPath: null,
	},
	transformState (state) {
		const { selectedPath } = state;

		if (selectedPath !== null) {
			return state;
		}

		return {
			...state,
			selectedPath: state.properties.selectedPath,
		};
	},
	properties: {
		table: {},
		fields: {},
		selectedPath: {},
		selectedLabel: {},
		dirty: {
			default: false,
		},
		placeholder: {},
	},
	view: filtersDotWalkView,
	actionHandlers: {
		'NOW_DOT_WALK_CONNECTED#PATH_UPDATED' ({
			action, properties, dispatch, updateState, updateProperties,
		}) {
			const { payload: { path, metadata } } = action;
			const { selectedPath } = properties;
			const { value, label } = getFieldsPathInfo(path);

			updateState({
				opened: false,
				selectedPath: value,
			});

			updateProperties({
				dirty: value !== selectedPath,
			});

			if (value !== selectedPath) {
				dispatch('PAR_FILTER_DOT_WALK#CHANGED', {
					value, label, path, metadata,
				});
			}
		},
	},
	eventHandlers: [
		{
			events: ['click'],
			effect ({ action, updateState }) {
				const { payload: { event: { path }, host } } = action;

				if (path.includes(host)) {
					return;
				}

				updateState({
					opened: false,
				});
			},
			target: document,
		},
	],
	styles,
};
