import { t } from 'sn-translate';
import styles from './boolean-content.scss';
import { NOW_FILTER__SET_DIRTY } from '../constants';

const getSelectedId = (ids) => {
	return ids.length ? ids[0] : null;
};

const properties = {
	items: {},
	selectedIds: {},
};

export const booleanContent = {
	properties,
	view ({ properties: { items, selectedIds } }) {
		return (
			<div>
				<div className="header">{t('Select Value')}</div>
				<now-select
					className="select"
					items={items}
					selectedItem={getSelectedId(selectedIds)}
				/>
			</div>
		);
	},
	styles,
	actionHandlers: {
		'NOW_SELECT#SELECTED_ITEM_SET' ({ properties, action, dispatch }) {
			const { selectedIds } = properties;
			const selectedId = getSelectedId(selectedIds);
			const { payload: { value } } = action;
			const dirty = value !== selectedId;

			dispatch(NOW_FILTER__SET_DIRTY, {
				dirty,
				dirtyBag: {
					selectedIds: [value],
					selectedItems: [value],
				},
			});
		},
	},
};
