import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-toggle';

import styles from './toggle.scss';

const initialState = {
	isChecked: null,
};
const properties = {
	disabled: {
		default: false,
	},
	label: {},
	size: {
		default: 'sm',
	},
	checked: {
		default: false,
	},
};

const transformState = (state) => {
	const { isChecked, properties: { checked } } = state;

	if (isChecked !== null) {
		return state;
	}

	return {
		...state,
		isChecked: checked,
	};
};

const toggleView = ({ isChecked, properties }, { dispatch }) => {
	const {
		label,
		size,
		disabled,
	} = properties;

	const handleClick = (e) => {
		if (e.path.findIndex((node) => node.tagName === 'NOW-TOGGLE') !== -1 || disabled) {
			return;
		}

		dispatch('PAR_FILTER_TOGGLE#CHECKED_SET', { value: !isChecked });
	};

	return (
		<div>
			<label on-click={handleClick}>
				<now-toggle
					disabled={disabled}
					checked={isChecked}
					manageChecked
					size={size}
					config-aria={{ 'aria-label': label }}
				/>
				<span className="label">{label}</span>
			</label>
		</div>
	);
};

const actionHandlers = {
	'PAR_FILTER_TOGGLE#CHECKED_SET' ({ action, updateState }) {
		const { payload: { value } } = action;

		updateState({
			isChecked: value,
		});
	},

	'NOW_TOGGLE#CHECKED_SET' ({ action, dispatch }) {
		const { payload: { value } } = action;
		dispatch('PAR_FILTER_TOGGLE#CHECKED_SET', { value });
		action.stopPropagation();
	},
};

createCustomElement('par-filter-config-toggle', {
	initialState,
	properties,
	transformState,
	view: toggleView,
	actionHandlers,
	styles,
	dispatches: {
		'PAR_FILTER_TOGGLE#CHECKED_SET': {},
	},
});
