import { t } from 'sn-translate';
import styles from './empty-option.scss';

const initialState = {
	checked: null,
};
const properties = {
	emptyOption: {
		default: null, // null | string
	},
};

const transformState = (state) => {
	const { checked, properties: { emptyOption } } = state;

	if (checked !== null) {
		return state;
	}

	return {
		...state,
		checked: Boolean(emptyOption),
	};
};

const emptyOptionView = ({ checked, properties }) => {
	const { emptyOption } = properties;

	return (
		<div>
			<par-filter-config-toggle label={t('Show empty values as one option?')} checked={checked} />

			{checked ? (
				<div className="input-container">
					<now-input
						className="input"
						autofocus
						type="text"
						label={t('Define the label for this option.')}
						placeholder={t('e.g. Unassigned')}
						value={emptyOption}
					/>
				</div>
			) : null}
		</div>
	);
};

const actionHandlers = {
	'PAR_FILTER_TOGGLE#CHECKED_SET' ({
		action, properties, updateState, dispatch,
	}) {
		const { payload: { value } } = action;
		const { emptyOption } = properties;
		const fieldValue = value ? emptyOption : null;

		updateState({ checked: value });
		dispatch('PAR_FILTER_EMPTY_OPTION#OPTION_SET', { value: fieldValue });
	},

	'NOW_INPUT#INPUT': {
		modifier: { name: 'debounce', delay: 250 },
		effect ({ action, dispatch }) {
			const { payload: { fieldValue } } = action;
			dispatch('PAR_FILTER_EMPTY_OPTION#OPTION_SET', { value: fieldValue.trim() || null });
		},
	},
};

export const emptyOption = {
	initialState,
	properties,
	transformState,
	view: emptyOptionView,
	actionHandlers,
	dispatches: {
		'PAR_FILTER_EMPTY_OPTION#OPTION_SET': {},
	},
	styles,
};
