/* eslint camelcase: 0 */

import { t } from 'sn-translate';
import { actionTypes } from '@servicenow/ui-core';
import orderBy from 'lodash/orderBy';

import { PAR_FILTER_SLUSH_BUCKET__MOVE_ITEMS } from '../../../slush-bucket/slush-bucket';
import { NOW_FILTER__SET_DIRTY } from '../../constants';
import { ListSort } from '../list-sort';
import { ListError } from '../list-error';
import filterItems from '../../../helpers/filterItems';
import searchItems from '../../../helpers/searchItems';

const PAR_FILTER_LIST_MULTI__SET_SLUSH_BUCKET_REF = 'PAR_FILTER_LIST_MULTI#SET_SLUSH_BUCKET_REF';
const PAR_FILTER_LIST_MULTI__SORT_TARGET = 'PAR_FILTER_LIST_MULTI#SORT_TARGET';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const properties = {
	items: {},
	selectedIds: {
		default: [],
	},
	sort: {},
	search: {},
	loading: {},
	errorMessage: {},
	onQuery: {},
	dirty: {
		default: false,
	},
};

const initialState = {
	slushBucket: null,
	availableItems: [],
	appliedItems: [],
	targetSort: 'ASC',
	isItemsSet: false,
};

function classifyItems (allItems, selectedIds) {
	const availableItems = [];
	const appliedItems = [];
	if (allItems && allItems.length) {
		for (const item of allItems) {
			if (selectedIds.includes(item.id)) {
				appliedItems.push(item);
			} else {
				availableItems.push(item);
			}
		}
	}
	return { availableItems, appliedItems };
}

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED] ({ properties: { items, selectedIds }, updateState }) {
		if (items) {
			const { availableItems, appliedItems } = classifyItems(items, selectedIds);
			updateState({
				availableItems,
				appliedItems,
				isItemsSet: true,
			});
		}
	},

	[COMPONENT_PROPERTY_CHANGED] ({
		action, state, properties: { items, selectedIds }, updateState,
	}) {
		if (action.payload.name === 'items') {
			const items = action.payload.value;
			if (!items) { return; }
			/**
			 * We need to preserve previously selected items by user even in case the items change
			 * This is happening when we have search and filtering the items externally as user types - (e.g. reference field)
			 */
			const isInit = !state.isItemsSet;
			const actualSelectedIds = isInit ? selectedIds : state.appliedItems.map((x) => x.id);
			const { availableItems, appliedItems } = classifyItems(items, actualSelectedIds);

			updateState({
				availableItems,
				appliedItems: isInit ? appliedItems : state.appliedItems,
				isItemsSet: true,
			});
		}

		if (
			action.payload.name === 'selectedIds' &&
			JSON.stringify(action.payload.value) !== JSON.stringify(action.payload.previousValue)
		) {
			const { availableItems, appliedItems } = classifyItems(items, selectedIds);
			updateState({ availableItems, appliedItems });
		}
	},

	[PAR_FILTER_LIST_MULTI__SET_SLUSH_BUCKET_REF] ({ action: { payload: { slushBucket } }, updateState }) {
		updateState({ slushBucket });
	},

	[PAR_FILTER_SLUSH_BUCKET__MOVE_ITEMS] ({
		action: { payload }, properties, state: { availableItems, appliedItems }, updateState, updateProperties, dispatch,
	}) {
		const { fromList /* source|target */, selectedItems } = payload;
		let newAvailable = availableItems;
		let newApplied = appliedItems;

		if (fromList === 'source') {
			newAvailable = filterItems(availableItems, selectedItems);
			newApplied = appliedItems.concat(selectedItems);
		}

		if (fromList === 'target') {
			newAvailable = availableItems.concat(selectedItems);
			newApplied = filterItems(appliedItems, selectedItems);
		}

		const newIds = new Set(newApplied.map((x) => x.id));
		const oldIds = new Set(properties.selectedIds);
		const dirty = newIds.size !== oldIds.size || [...newIds].some((x) => !oldIds.has(x));

		updateProperties({ dirty });
		updateState({
			availableItems: newAvailable,
			appliedItems: newApplied,
		});
		dispatch(NOW_FILTER__SET_DIRTY, {
			dirty,
			dirtyBag: {
				selectedIds: newApplied.map((x) => x.id),
				selectedItems: newApplied,
			},
		});
	},

	[PAR_FILTER_LIST_MULTI__SORT_TARGET] ({ action: { payload: { sort } }, updateState }) {
		updateState({ targetSort: sort });
	},
};

const view = (state, { dispatch }) => {
	const {
		availableItems, appliedItems, properties, targetSort,
	} = state;
	const {
		dirty,
		sort,
		search,
		loading,
		errorMessage,
		onQuery,
	} = properties;

	const onTargetSort = (params) => {
		dispatch(PAR_FILTER_LIST_MULTI__SORT_TARGET, params);
	};

	let availableItemsToShow = searchItems(availableItems, search);
	availableItemsToShow = orderBy(availableItemsToShow, ['label'], [sort.toLowerCase()]);
	let appliedItemsToShow = searchItems(appliedItems, search);
	appliedItemsToShow = orderBy(appliedItemsToShow, ['label'], [targetSort.toLowerCase()]);

	if (errorMessage) {
		return (
			<div className="message-container">
				<ListError errorMessage={errorMessage} />
			</div>
		);
	}

	return (
		<par-filter-slush-bucket
			hook-insert={({ elm }) => dispatch(PAR_FILTER_LIST_MULTI__SET_SLUSH_BUCKET_REF, { slushBucket: elm })}
			loading={loading}
			sourceTitle={t('Available')}
			sourceSortAction={() => (
				<ListSort
					testId="data-test-sort-button-available-items"
					sort={sort}
					onAction={onQuery}
					disabled={!availableItemsToShow || availableItemsToShow.length === 0}
				/>
			)}
			targetTitle={dirty ? t('To be applied') : t('Applied')}
			targetSortAction={() => (
				<ListSort
					testId="data-test-sort-button-applied-items"
					sort={targetSort}
					onAction={onTargetSort}
					disabled={!appliedItemsToShow || appliedItemsToShow.length === 0}
				/>
			)}
			sourceItems={availableItemsToShow}
			targetItems={appliedItemsToShow}
		/>
	);
};

export const listMulti = {
	initialState,
	properties,
	actionHandlers,
	view,
	styles: `
		:host {
		  display: contents;
		}
	`,
};
