import { t } from 'sn-translate';

const getSortingActions = (sort, onAction) => {
	return sort === 'ASC' ? {
		icon: 'sort-descending-fill',
		tooltip: t('Sort descending'),
		onClick: () => onAction({ sort: 'DESC' }),
	} : {
		icon: 'sort-ascending-fill',
		tooltip: t('Sort ascending'),
		onClick: () => onAction({ sort: 'ASC' }),
	};
};

export function ListSort ({
	testId,
	sort,
	disabled,
	onAction,
}) {
	const {
		icon,
		tooltip,
		onClick,
	} = getSortingActions(sort, onAction);

	return (
		<now-button-iconic
			{...{ [`attr-${testId}`]: true }}
			bare
			icon={icon}
			size="md"
			tooltip-content={tooltip}
			variant="tertiary"
			on-click={onClick}
			disabled={disabled}
		/>
	);
}
