import { t } from 'sn-translate';
import styles from './slush-bucket.scss';

export const PAR_FILTER_SLUSH_BUCKET__MOVE_ITEMS = 'PAR_FILTER_SLUSH_BUCKET#MOVE_ITEMS';

const refs = {
	sourceList: null,
	targetList: null,
};

const initialState = {
	moveButtonsDisabled: {
		source: true,
		target: true,
	},
};

const properties = {
	sourceTitle: {
		default: '',
	},
	sourceSortAction: {
		default: () => {},
	},
	targetTitle: {
		default: '',
	},
	targetSortAction: {
		default: () => {},
	},
	sourceItems: {
		default: [],
	},
	targetItems: {
		default: [],
	},
	loading: {
		default: false,
	},
};

const Loading = ({ state }) => (state ? (
	<div className="loading">
		<now-loader size="lg" />
	</div>
) : null);

const NoItems = ({ condition, ...props }) => (condition ? (
	<div className="no-content" {...props}>{t('Nothing is available')}</div>
) : null);

export const view = ({ moveButtonsDisabled, properties }, { dispatch, updateState }) => {
	const {
		sourceTitle,
		sourceSortAction,
		targetTitle,
		targetSortAction,
		sourceItems,
		targetItems,
		loading,
	} = properties;

	const move = (fromList) => () => {
		const { selectedItems } = refs[`${fromList}List`];

		dispatch(PAR_FILTER_SLUSH_BUCKET__MOVE_ITEMS, {
			fromList,
			selectedItems,
		});

		updateState({
			operation: 'set',
			path: `moveButtonsDisabled[${fromList}]`,
			value: true,
		});
	};

	const handleListboxChange = (list) => (selectedItems) => {
		updateState({
			operation: 'set',
			path: `moveButtonsDisabled[${list}]`,
			value: selectedItems.length === 0,
		});
	};

	return (
		<div className="container">
			<div className="column">
				<div className="header">
					<h3 className="title">
						{sourceTitle}
						{sourceSortAction()}
					</h3>
				</div>
				<div className="items">
					<Loading state={loading} />
					<NoItems condition={sourceItems && sourceItems.length === 0 && !loading} data-testNoAvailableItems />
					<par-filter-listbox
						append-to-payload={{ list: 'source' }}
						items={sourceItems}
						label={sourceTitle}
						hook-insert={({ elm }) => {
							refs.sourceList = elm;
						}}
						onChange={handleListboxChange('source')}
					/>
				</div>
			</div>

			<div className="actions">
				<now-button-iconic
					data-testMoveToAppliedButton
					bare
					className="button"
					icon="chevron-end-fill"
					size="md"
					tooltip-content={t('Select values')}
					variant="tertiary"
					on-click={move('source')}
					disabled={moveButtonsDisabled.source}
				/>
				<div className="separator" />
				<now-button-iconic
					data-testMoveToAvailableButton
					bare
					className="button"
					icon="chevron-start-fill"
					tooltip-content={t('Unselect values')}
					size="md"
					variant="tertiary"
					on-click={move('target')}
					disabled={moveButtonsDisabled.target}
				/>
			</div>

			<div className="column">
				<div className="header">
					<h3 className="title" id="appliedItemsLabel">
						<span data-testAppliedItemsTitle>{targetTitle}</span>
						{targetSortAction()}
					</h3>
				</div>
				<div className="items">
					<Loading state={loading} />
					<NoItems condition={targetItems && targetItems.length === 0 && !loading} data-testNoAppliedItems />
					<par-filter-listbox
						append-to-payload={{ list: 'target' }}
						items={targetItems}
						label={targetTitle}
						hook-insert={({ elm }) => {
							refs.targetList = elm;
						}}
						onChange={handleListboxChange('target')}
					/>
				</div>
			</div>
		</div>
	);
};

export const slushBucket = {
	initialState,
	properties,
	view,
	styles,
};
