import { Fragment } from '@servicenow/ui-renderer-snabbdom';

import {
	NOW_FILTER__TOGGLED,
	NOW_FILTER__APPLIED,
	NOW_FILTER__SET_DIRTY,
	NOW_FILTER__CLEARED,
} from './constants';
import { Badge } from './badge';
import { UnconfiguredContent } from './unconfigured-content';
import behaviors from './behaviors';
import styles from './filter.scss';

const getAppliedCount = (selectedIds) => {
	if (Array.isArray(selectedIds)) {
		return selectedIds.length;
	}

	return 0;
};

const properties = {
	/**
	 * Filter label to display in a pill
	 */
	label: {
		default: '',
	},

	/**
	 * Type of the filter. Supported types are `choice`, `daterange` and `boolean`.
	 * choice - renders multi or single select list of items.
	 * daterange - renders calendar to select custom range and a list of options to select from predefened date ranges presets.
	 * boolean - renders single select list designed for boolean selection.
	 */
	type: {
		schema: {
			type: 'string',
			enum: [
				'choice',
				'daterange',
				'boolean',
			],
		},
	},

	/**
	 * Single or multiple selection type of filter. Supported values are multi and single.
	 */
	listMode: {
		default: 'multi',
		schema: {
			type: 'string',
			enum: [
				'multi',
				'single',
			],
		},
	},

	sort: {
		default: 'ASC',
		schema: {
			type: 'string',
			enum: [
				'ASC',
				'DESC',
			],
		},
	},

	search: {},
	items: {},
	selectedIds: {},
	loading: {},
	errorMessage: {},
	start: {},
	end: {},
	opened: {
		default: false,
	},
};

const initialState = {
	dirty: false,
	dirtyBag: {},
	selectedIds: null,
	start: null,
	end: null,
};

export const view = ({
	dirty, dirtyBag, selectedIds, start, end, properties,
}, { dispatch }) => {
	const {
		opened,
		label,
		type,
		items,
		listMode,
		sort,
		search,
		loading,
		errorMessage,
	} = properties;

	const handleCancel = () => {
		dispatch(NOW_FILTER__TOGGLED, { value: !opened });
	};

	const handleApply = () => {
		dispatch(NOW_FILTER__APPLIED, dirtyBag);
		dispatch(NOW_FILTER__TOGGLED, { value: false });
	};

	const handleClear = () => {
		dispatch(NOW_FILTER__CLEARED);
	};

	const isBoolean = type === 'boolean';
	const isList = type === 'choice';
	const appliedCount = getAppliedCount(selectedIds);
	const unconfigured = !type;
	const canClear = isList || isBoolean ? appliedCount > 0 : (start && end);
	const getPopupContent = () => {
		if (unconfigured) {
			return (
				<UnconfiguredContent />
			);
		}

		if (type === 'daterange') {
			return (
				<par-filter-daterange-content
					start={start}
					end={end}
				/>
			);
		}

		if (type === 'boolean') {
			return (
				<par-filter-boolean-content
					items={items}
					selectedIds={selectedIds}
				/>
			);
		}

		return (
			<par-filter-list-content
				listMode={listMode}
				sort={sort}
				search={search}
				items={items}
				selectedIds={selectedIds}
				loading={loading}
				errorMessage={errorMessage}
				dirty={dirty}
			/>
		);
	};

	const popupStyles = {
		width: isList || unconfigured ? (listMode === 'single' ? 'auto' : '560px') : 'auto',
		minWidth: listMode === 'single' ? '340px' : 'auto',
		maxWidth: listMode === 'single' ? '560px' : 'auto',
		height: isList ? '536px' : 'auto',
	};

	return (
		<now-popover
			manageOpened
			opened={opened}
			positions={[
				{ target: 'bottom-start', content: 'top-start' },
				{ target: 'bottom-end', content: 'top-end' },
				{ target: 'top-start', content: 'bottom-start' },
				{ target: 'top-end', content: 'bottom-end' },
			]}
		>
			<div
				tabIndex="0"
				slot="trigger"
				class={{
					container: true,
					opened: opened,
					hasBadge: appliedCount > 0 || Boolean(start),
				}}
				role="button"
				data-testFilterContainer
			>
				{appliedCount > 0 ? <Badge content={appliedCount} /> : ''}
				{start ? <Badge content={<now-icon icon="calendar-outline" />} /> : ''}
				<div key="name" className="name">
					<span>{label}</span>
					{start ? (
						<Fragment>
							<span className="colon">:</span>
							<span>{start} - {end}</span>
						</Fragment>
					) : ''}
				</div>
				<now-icon icon="chevron-down-fill" className="chevron" size="md" />
			</div>

			{opened ? (
				<par-filter-popup
					style={popupStyles}
					slot="content"
					dirty={dirty}
					canClear={canClear}
					onClose={handleCancel}
					onApply={handleApply}
					onClear={handleClear}
				>
					{getPopupContent()}
				</par-filter-popup>
			) : ''}
		</now-popover>
	);
};

export const filter = {
	properties,
	initialState,
	behaviors,
	transformState (state) {
		const {
			selectedIds, start, end, properties,
		} = state;

		if (selectedIds !== null || (start && end)) {
			return state;
		}

		return {
			...state,
			selectedIds: properties.selectedIds,
			start: properties.start,
			end: properties.end,
		};
	},
	actionHandlers: {
		'NOW_POPOVER#OPENED_SET': ({ action, dispatch }) => {
			const { payload: { value } } = action;
			dispatch(NOW_FILTER__TOGGLED, { value });
		},

		[NOW_FILTER__TOGGLED] ({ action, updateProperties, updateState }) {
			const { payload: { value } } = action;
			updateProperties({
				opened: value,
			});

			if (!value) {
				updateState({
					dirty: false,
					dirtyBag: {},
					selectedIds: null,
					start: undefined,
					end: undefined,
				});
			}
		},

		[NOW_FILTER__SET_DIRTY] ({ action, updateState }) {
			const { payload: { dirty, dirtyBag } } = action;
			updateState({ dirty, dirtyBag });
		},

		[NOW_FILTER__CLEARED] ({ updateState, properties }) {
			const { type } = properties;
			const dirtyBag = type === 'daterange'
				? { start: undefined, end: undefined }
				: { selectedIds: [], selectedItems: [] };
			const props = type === 'daterange' ? { start: undefined, end: undefined } : {};

			updateState({
				selectedIds: [],
				dirty: true,
				dirtyBag,
				...props,
			});
		},
	},
	styles,
	view,
};
