import rtlBehavior from '@servicenow/behavior-rtl';
import { t } from 'sn-translate';

import { ListSearch } from './list-search';
import styles from './list-content.scss';

import { NOW_FILTER__QUERIED, NOW_FILTER__SORTED } from '../constants';

export const listContentView = ({ properties }, { dispatch }) => {
	const {
		dirty, listMode, sort, search, items, selectedIds, loading, errorMessage,
	} = properties;
	const onQuery = (params) => {
		if (params.search !== undefined) {
			dispatch(NOW_FILTER__QUERIED, params);
		}
		if (params.sort) {
			dispatch(NOW_FILTER__SORTED, params);
		}
	};
	const props = {
		dirty,
		onQuery,
		sort,
		search,
		items,
		selectedIds,
		loading,
		errorMessage,
	};

	if (listMode === 'single') {
		return (
			<div className="list-container">
				<par-filter-list-single {...props} />
			</div>
		);
	}

	return (
		<div className="list-container">
			<ListSearch
				search={search}
				ariaLabel={t('Filter available and applied items.')}
				onChange={(value) => onQuery({ search: value })}
			/>
			<par-filter-list-multi {...props} />
		</div>
	);
};

export const listContent = {
	properties: {
		listMode: {},
		sort: {
			default: 'ASC',
		},
		search: {},
		items: {},
		selectedIds: {
			default: [],
		},
		loading: {},
		errorMessage: {},
		dirty: {
			default: false,
		},
	},
	behaviors: [
		{ behavior: rtlBehavior },
	],
	styles,
	view: listContentView,
};
