import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import focusBehavior from '@servicenow/behavior-focus';
import rtlBehavior from '@servicenow/behavior-rtl';

import { VanillaListbox } from './vanilla-listbox';
import styles from './listbox.scss';

const HOOK_INSERTED = 'HOOK_INSERTED';

const properties = {
	multiple: {
		default: true,
	},
	label: {},
	items: {
		default: null,
	},
	selectedItems: {
		default: [],
	},
	onChange: {
		default: () => {},
	},
};

const actionHandlers = {
	[HOOK_INSERTED] ({ action, properties, updateProperties }) {
		const { payload: { listbox } } = action;
		const { onChange, multiple } = properties;

		new VanillaListbox({
			multiple,
			listbox,
			onChange (selectedItems) {
				updateProperties({ selectedItems });
				onChange(selectedItems);
			},
		});
	},
};

export const view = ({ properties }, { dispatch }) => {
	const { items, label, multiple } = properties;

	let index = -1;
	const renderItems = (itemsList) => {
		if (!itemsList || itemsList.length === 0) {
			return null;
		}

		return itemsList.map(({ label, id, items }) => {
			if (items !== undefined) {
				return (
					<li className="group-option">
						<ul className="group">
							<li className="group-label" {...{ attrs: { onclick: 'this.parentNode.classList.toggle("collapsed")' } }}>{label}</li>
							{renderItems(items)}
						</ul>
					</li>
				);
			}

			index += 1;

			return (
				<li
					key={`${id}_${index}_${itemsList.length}`}
					role="option"
					aria-selected="false"
					className="option"
					id={`option_${index}`}
					data-index={String(index)}
					data-id={id}
				>
					{label}
				</li>
			);
		});
	};

	return (
		<Fragment>
			<ul
				hook-insert={({ elm }) => dispatch(HOOK_INSERTED, { listbox: elm })}
				className="listbox"
				tabIndex="0"
				role="listbox"
				aria-multiselectable={String(multiple)}
				aria-label={label}
			>
				{renderItems(items)}
			</ul>
		</Fragment>
	);
};

const behaviors = [
	{ behavior: focusBehavior },
	{ behavior: rtlBehavior },
];

export const listbox = {
	properties,
	actionHandlers,
	view,
	styles,
	behaviors,
};
