import moment from 'moment';

export const toMoment = (date) => (date ? moment(date) : undefined);

export const fromMoment = (m) => moment(m).format('YYYY-MM-DD');

/**
 *	This helper has the business logic that figures out if a range
 *	is done, or just the start with no end yet.
 *
 *	It's used to make the date panel show highlights when you've
 *	selected the start, but not yet the end.
 *
 *	I created this because I didn't want to have another internal
 *	state variable to track how complete the range is.
 */
export const getStartEnd = (start, end, date) => {
	const first = end === undefined;
	const one = fromMoment(date);
	const two = first
		? fromMoment(start)
		: undefined;

	return [one, two].sort();
};
