/**
 * The purpose of this JSX component is a lightweight radio group which has radio inputs hidden.
 * Existing radio group components from DEVX and NDS both don't allow hiding radio buttons.
 * @param options {Array<{value: String, text: String }>} Radio buttons options.
 * @param checkedOption {{ value: String }} Object with selected option value.
 * @param dispatch {Function} Seismic component dispatch function.
 * @param groupName {String} Name of the radio group.
 * @constructor
 */
export const RadioGroup = ({
	options, checkedOption = {}, dispatch, groupName = 'radioGroup',
}) => {
	const handleChange = ({ target: { value } }) => {
		dispatch('RADIO_GROUP#CHECKED', { value });
	};

	return (
		<fieldset
			data-testRadioGroup
			id={groupName}
			className="radio-group"
			on-change={handleChange}
		>
			{options.map(({ value, text }, index) => (
				<div key={value} className="radio-item">
					<input
						type="radio"
						value={value || ''}
						name={groupName}
						id={`input-${index}`}
						className="radio-item-input"
						{...value === checkedOption.value && { checked: true }}
					/>
					<label
						htmlFor={`input-${index}`}
						className="radio-item-label"
					>
						{text}
					</label>
				</div>
			))}
		</fieldset>
	);
};
