/**
 * Script used by filter components as an event handler in UIB.
 *
 * @param {Array<object>} oldFilters Applied filters UIB page state.
 * @param {object} newFilters Payload dispatched by a filter.
 * @returns {Array} New merged filters state.
 */
export function mergePARFilters (oldFilters, newFilters) {
	const mergedFilters = oldFilters === undefined
		? []
		: [...oldFilters];

	for (const filterItem of newFilters) {
		if (filterItem) {
			const domainFilter = filterItem.type === 'domain';
			const dateFilter = filterItem.type === 'date';
			let index = 0;

			if (domainFilter) {
				index = mergedFilters.findIndex((x) => x.type === 'domain');
			} else if (Array.isArray(filterItem.apply_to)) {
				index = mergedFilters.findIndex((x) => x.apply_to && x.apply_to[0] === filterItem.apply_to[0]);
			}

			if (index >= 0) {
				if ((domainFilter && !filterItem.sys_id) || (dateFilter && !filterItem.start && !filterItem.end)) {
					mergedFilters.splice(index, 1);
				} else {
					mergedFilters[index] = filterItem;
				}
			} else {
				mergedFilters.push(filterItem);
			}
		}
	}

	mergedFilters.forEach((x, i) => {
		x.order = i;
	});

	return mergedFilters;
}
