import { t } from 'sn-translate';
import { actionTypes } from '@servicenow/ui-core';

import { NOW_FILTER__APPLIED } from '../filter';

export const SN_COMPONENT_FILTER__APPLIED_ITEMS = 'SN_COMPONENT_FILTER#APPLIED_ITEMS';

function assertTableFields (tablefields) {
	if (
		!Array.isArray(tablefields) ||
		tablefields.length === 0 ||
		!tablefields.every((tf) => tf && tf.table && tf.table.id && tf.field && tf.field.id && tf.field.type)
	) {
		throw new Error('tablefields are not valid');
	}
}

const getAppliedFilters = (label, props, payload) => {
	const tablefields = props.tablefields || [];
	assertTableFields(tablefields);
	const firstTablefield = tablefields[0];
	const fieldType = firstTablefield.field.type;

	// Manually checked with glide/src/com/glide/db/IDBTypes.java
	const isDateFilter = /date/i.test(fieldType);
	const { emptyOption } = props;

	let newPayload;

	if (isDateFilter) {
		newPayload = {
			order: 9000,
			type: 'date',
			label,
			apply_to: tablefields.map((x) => `${x.table.id}.${x.field.id}`),
			start: payload.start,
			end: payload.end,
		};
	} else {
		const values = payload.selectedIds || [];

		newPayload = {
			order: 9000,
			type: fieldType,
			label,
			apply_to: tablefields.map((x) => `${x.table.id}.${x.field.id}`),
			values,
			include_unmatched_or_empty: (payload.selectedItems || [])
				.some((item) => (
					item.id === '' &&
					// second one is for Choices and comes from the server like that
					(item.label === emptyOption || item.label === t('-- None --'))
				)),
		};
	}
	return [newPayload];
};

export const filterConnectedUxf = {
	properties: {
		/**
		 * Name of the filter displayed as a label.
		 */
		filterName: {
			default: 'Filter',
		},
		/**
		 * JSON stringified filter configuration that includes array of tablefields object, encodedQuery and listMode.
		 */
		filterProperties: {
			schema: {
				type: 'string',
			},
		},
		/**
		 * Array of applied filters
		 */
		filterConfigurations: {
			default: [],
		},
	},
	actionHandlers: {
		[NOW_FILTER__APPLIED] ({
			action: { payload }, properties, dispatch,
		}) {
			// TODO: refactor both the sidebar and the filter to cleanup the filterProperties data structure
			const filterProps = JSON.parse(properties.filterProperties || '{}');

			const appliedFilters = getAppliedFilters(properties.filterName, filterProps, payload);

			dispatch(SN_COMPONENT_FILTER__APPLIED_ITEMS, { appliedFilters });
		},
		[actionTypes.COMPONENT_BOOTSTRAPPED] ({ properties, dispatch }) {
			const props = JSON.parse(properties.filterProperties || '{}');
			const confs = properties.filterConfigurations;

			if (Array.isArray(confs) && confs.length === 0) {
				let appliedFilters;

				if (props.defaults && props.defaults.length) {
					appliedFilters = getAppliedFilters(properties.filterName, props, {
						selectedIds: props.defaults.map((d) => d.id),
					});
				} else if (props.defaults && (props.defaults.start || props.defaults.end)) {
					appliedFilters = getAppliedFilters(properties.filterName, props, {
						start: props.defaults.start,
						end: props.defaults.end,
					});
				} else {
					return;
				}

				dispatch(SN_COMPONENT_FILTER__APPLIED_ITEMS, { appliedFilters });

				// Workaround for UXF losing events on startup
				setTimeout(() => {
					dispatch(SN_COMPONENT_FILTER__APPLIED_ITEMS, { appliedFilters });
				}, 100);
			}
		},
	},
	view (state) {
		const { properties } = state;
		const { filterProperties, filterConfigurations, filterName } = properties;

		// TODO: refactor both the sidebar and the filter to cleanup the filterProperties data structure
		const {
			tablefields = [],
			encodedQuery = '',
			emptyOption = null,
			listMode = 'multi',
			sort = 'ASC',
			defaults = [],
			booleanLabels = {},
		} = JSON.parse(filterProperties || '{}');
		const table = (tablefields[0] && tablefields[0].table && tablefields[0].table.id) || '';
		const field = (tablefields[0] && tablefields[0].field && tablefields[0].field.id) || '';
		const fieldType = (tablefields[0] && tablefields[0].field && tablefields[0].field.type) || '';

		const appliedFilter = (
			Array.isArray(filterConfigurations) &&
			filterConfigurations.find((x) => x && Array.isArray(x.apply_to) && x.apply_to.length > 0 && x.apply_to[0] === `${table}.${field}`)
		) || {};
		const selectedIds = appliedFilter.values !== undefined
			? appliedFilter.values
			: defaults.length ? defaults.map((val) => val.id) : [];

		return (
			<now-filter-connected
				table={table}
				field={field}
				fieldType={fieldType}
				encodedQuery={encodedQuery}
				emptyOption={emptyOption}
				booleanLabels={booleanLabels}
				sort={sort}
				label={filterName}
				selectedIds={selectedIds}
				listMode={listMode}
				start={appliedFilter.start}
				end={appliedFilter.end}
			/>
		);
	},
	styles: `
		:host {
			/*
			UI builder has CSS rules to make every slotted component block level:
			::slotted(*) { display: block; }
			that's why we have to use !important to overrite it
			(tested: :host(sn-component-filter) { display: inline-block; } doesn't work).
			*/
			display: inline-block !important;
			min-width: 100px;
			max-width: 300px;
		}
	`,
	dispatches: {
		[SN_COMPONENT_FILTER__APPLIED_ITEMS]: { schema: { type: 'object' } },
	},
};
