import moment from 'moment';

export const today = new Date()
	.toISOString()
	.replace(/T.*$/, '');

/**
 *	A little helper to turn the presets into options for devx-radio-group
 */
export const toOption = (preset) => ({
	value: preset.range,
	text: preset.label,
});

/**
 *	A range parser that enables keeping the presets nice and simple.
 */
export const toStart = (range) => {
	const [offset, unit] = range ? range.split(' ') : [];

	return offset === 'year'
		// currently only supports 'Year to date'
		? moment().startOf('year').format('YYYY-MM-DD')
		// and generic 'offset unit' pairs
		: moment().add(offset, unit).format('YYYY-MM-DD');
};

/**
 *	Of course, having a mapping from preset to [start,end] means we also need the reverse.
 *	(to highlight the radio-group when you select a [start,end] that matches a preset)
 */
export const matchRange = (presets, start, end) => {
	const preset = presets
		.find((preset) => toStart(preset.range) === start);

	if (end !== today || !preset) {
		return {};
	}

	return { value: preset.range };
};
