import { t } from 'sn-translate';
import { actionTypes } from '@servicenow/ui-core';

import styles from './tablefield-popover.scss';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

const shipTableFieldData = (tables, table, fieldInfo) => {
	const fullTable = tables.find(({ value }) => value === table.id);
	const {
		value, label, path, metadata,
	} = fieldInfo;
	const lastSection = path[path.length - 1].field;
	const lastSectionMetadata = metadata[lastSection];
	const {
		type, choices, reference, reference_display_field,
	} = lastSectionMetadata;
	const referenceLabel = reference
		? tables.find(({ value }) => value === reference).label
		: '';

	return {
		table: {
			id: fullTable.value,
			label: fullTable.label,
		},
		field: {
			id: value,
			label,
			type,
			choices,
			choiceLabel: 'Choice',
			reference,
			referenceField: reference_display_field,
			referenceLabel,
		},
	};
};

export const tablefieldPopoverView = ({
	search, table, field, fieldInfo, properties,
}, { updateState }) => {
	const {
		opened,
		tables,
		editTable,
		editField,
		errors,
		targetRef,
		onClose,
		onSave,
	} = properties;

	const items = search.header ? [{ label: search.header, children: search.matches }] : [];
	const buttonEnabled =	table && field &&
		(
			(!editTable && !editField && table && field) ||
			(editTable && editTable.id !== table.id) ||
			(editField && editField !== field)
		);

	const handleSave = () => {
		const tableFieldData = shipTableFieldData(tables, table, fieldInfo);

		updateState({
			table: undefined,
			field: undefined,
		});
		onSave(tableFieldData);
	};
	const handleClose = () => {
		updateState({
			table: undefined,
			field: undefined,
		});
		onClose();
	};

	return (
		<sn-par-popover
			position={[
				'center-start center-end',
			]}
			target-ref={targetRef}
			opened={opened}
		>
			<div className="overlay" on-click={handleClose} />
			<div className="popover">
				<now-heading
					label={t('Data tables')}
					level="3"
					variant="title-tertiary"
				/>
				<now-button-iconic
					bare
					icon="close-fill"
					size="md"
					tooltip-content={t('Close popover')}
					variant="primary"
					on-click={handleClose}
				/>

				<now-typeahead
					className="control"
					label={t('Table')}
					appendToPayload={{ type: 'table' }}
					items={items}
					selectedItem={table ? table.id : null}
					value={table && table.label ? table.label : ''}
					placeholder={t('Select a table')}
					messages={errors.tables ? [{ status: 'critical', content: t('Error loading tables') }] : null}
					manageSelectedItem
				>
					<now-icon icon="caret-down-fill" className="caret" slot="end" />
				</now-typeahead>

				{opened && table ? (
					<par-filter-config-dot-walk
						placeholder={t('Select a field')}
						className="dot-walk"
						label={t('Field')}
						table={table.id}
						tablesItems={search.tables}
						selectedPath={(field ? field.value : '')}
						selectedLabel={(field ? field.label : '')}
					/>
				) : null}

				<now-button
					label={t('Save')}
					size="md"
					variant="primary"
					disabled={!buttonEnabled}
					on-click={handleSave}
				/>
			</div>
		</sn-par-popover>
	);
};

export const tablefieldPopover = {
	initialState: {
		search: {
			header: '',
			matches: [],
		},
		tables: null,
		table: '',
		field: '',
		fieldInfo: {},
	},
	properties: {
		tables: {},
		editTable: {},
		editField: {},
		errors: {},
		opened: {},
		targetRef: {},
		onClose: {},
		onSave: {},
	},
	view: tablefieldPopoverView,
	styles,
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED] ({ action, properties, updateState }) {
			const { payload: { name } } = action;
			const {
				editTable, editField, opened, tables,
			} = properties;

			if (name === 'opened' || name === 'targetRef') {
				updateState({
					table: editTable,
					field: editField,
				});
			}

			if (name === 'opened' && !opened) {
				updateState({
					fieldInfo: {},
				});
			}

			if (name === 'tables' && tables.length > 0) {
				updateState({
					tables: properties.tables.map(({ label, value }) => ({ id: value, label: `${label} [${value}]` })),
				});
			}
		},

		'NOW_TYPEAHEAD#VALUE_SET' ({ state, action, updateState }) {
			const { payload: { value } } = action;
			const { tables } = state;

			if (value.length < 3) {
				updateState({
					search: {
						header: t('Enter at least 3 characters to begin searching'),
						matches: [],
					},
				});
			} else if (tables) {
				const matches = tables.filter((item) => item.label.toLowerCase().includes(value.toLowerCase()));
				const totalCount = tables.length;
				const header = totalCount
					? `Showing 1-${matches.length} of ${totalCount} Results`
					: 'No results found';

				updateState({
					search: {
						header,
						matches,
					},
				});
			}
		},

		'NOW_TYPEAHEAD#SELECTED_ITEM_SET' ({ action, state, updateState }) {
			const { payload: { item } } = action;
			const { table } = state;

			if (action.payload && action.payload.type === 'table' && item.id !== null && table.id !== item.id) {
				updateState({
					table: item,
					field: '',
				});
			}
		},

		'PAR_FILTER_DOT_WALK#CHANGED' ({ action, updateState }) {
			const {
				payload: {
					value, metadata, path, label,
				},
			} = action;

			updateState({
				field: {
					value,
					label,
				},
				fieldInfo: {
					metadata, path, value, label,
				},
			});
		},
	},
};
