import { snHttpFactory } from 'sn-http-request';

const countUrl = '/api/now/table';
const snHttp = snHttpFactory({
	xsrfToken: window.g_ck,
	xTransactionSource: window.transaction_source,
});

// We cannot use createHttpEffect because it swallows the headers returned from
// the server. So we use what createHttpEffect uses: sn-http-request.
//
// The call is abstracted here so it is easy to replace with a bespoke backend.
export const getCount = (table, query = '') => {
	return snHttp.request(`${countUrl}/${table}`, 'get', {
		params: {
			sysparm_limit: 1,
			sysparm_fields: 'sys_id',
			sysparm_query: query,
		},
		batch: false,
	})
		.then((e) => e.headers['x-total-count']);
};
